import React from 'react';
import imageDrag from '../../images/drag.svg';
import Dropzone from 'react-dropzone';
import { toastr } from 'react-redux-toastr';
import 'react-redux-toastr/lib/css/react-redux-toastr.min.css';
import * as Utils from './Utils';
import * as Consts from '../../shared/Consts';
import uuid from 'uuid';

export default class FileUploader extends React.Component {
  constructor (props) {
    super(props);
    this.handleOnDrag = this.handleOnDrag.bind(this);

    this.state = {
      AcceptedFiles: undefined
    };
  }

  handleOnDrag (acceptedFiles, rejectedFiles) {
    if (rejectedFiles !== undefined && rejectedFiles.length > 1) {
      toastr.warning('File upload', 'Please select single file to import');
      return;
    }

    const selectedFiles = [];
    acceptedFiles.forEach(file => {
      selectedFiles.push(
        {
          id: uuid(),
          action: Consts.FILE_UPLOADER_ACTION_ADD,
          name: file.name,
          type: file.type,
          size: file.size,
          file: file
        }
      );
    });

    this.setState({ AcceptedFiles: selectedFiles });
    this.props.ValueChangeHandler(this, this.props.FieldName, this.props.FieldType, selectedFiles);
  }

  render () {
    const {
      FieldName,
      Multiple,
      Message,
      InputValues,
      ValidationResults
    } = this.props;

    const validationError = Utils.GetValidationResult(ValidationResults, FieldName);

    return (
      <>
        <Dropzone
          multiple={Multiple}
          onDrop={this.handleOnDrag}
          disabled={this.props.ReadOnly === true}
        >
          {({ getRootProps, getInputProps }) => (
            <div className='uploader file-uploader' {...getRootProps()}>
              <input {...getInputProps()} />
              <div className='inputfile' disabled={this.props.ReadOnly === true ? 'disabled' : ''} />
              <label className='file-uploader-label'>
                {this.props.ReadOnly !== true &&
                  <div className='uploads'>
                    <div className='up_btn'>
                      <img src={imageDrag} alt='Upload file' width='100%' />
                    </div>

                    <span>Drag &amp; Drop (or) <div className='choose animated_fastest'>Choose File &gt;</div></span>
                  </div>}
                {
                  (Message !== undefined && Message.trim() !== '') &&
                    <div className='acct'>{Message}</div>
                }
                {
                  InputValues[FieldName] !== undefined &&
                  InputValues[FieldName].map((file, index) =>
                    <div key={index}>{file.name}</div>
                  )
                }
              </label>
              {
                validationError !== undefined &&
                  <>
                    <div className='validation-failed' />
                    {validationError}
                  </>
              }
            </div>
          )}
        </Dropzone>

      </>
    );
  }
}
