import React from 'react';

export default class BuyerProfileFooter extends React.Component {
  render () {
    const {
      SaveHandler,
      PreviousStepHandler
    } = this.props;

    return (
      <>
        <div className='prev'>
          <div className='prev_btn animated_fastest' onClick={PreviousStepHandler}>Previous</div>
        </div>
        <div className='nexts'>
          <div className='save_btn animated_fastest' onClick={SaveHandler}>Save</div>
        </div>
        <div className='clearer' />
      </>
    );
  }
}
