import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class ExteriorGrounds extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Exterior & Grounds' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_144_Exterior_Features' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_144_Exterior_Features', 'Fences') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_625_Defects' })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_625_Defects', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_625_t_Explanation' })}</div><br />
                    </>
                }

              </>
          }

          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_144_Exterior_Features', 'RetainingWalls') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_521_Defects' })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_521_Defects', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_521_t_Explanation' })}</div><br />
                    </>
                }

              </>
          }

          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_144_Exterior_Features', 'Sidewalk') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_102_Defects' })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_102_Defects', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_102_t_Explanation' })}</div><br />
                    </>
                }

              </>
          }

          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_144_Exterior_Features', 'PondsWaterFeatures') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_209_Defects' })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_209_Defects', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_209_t_Explanation' })}</div><br />
                    </>
                }

              </>
          }

          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_144_Exterior_Features', 'Sprinkler') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_947_Sprinkler_Type' })}</div><br />
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_338_Not_Covered_By_Sprinkler' })}</div>
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_338_Not_Covered_By_Sprinkler', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_338_t_Description' })}</div><br />
                    </>
                }

              </>
          }

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_765_Defects' })}</div><br />
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_930_Defects' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFX_930_Defects', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFX_930_t_Text_Explanation' })}</div><br />
              </>
          }
        </div>
      </>
    );
  }
}
