import React from 'react';
import FooterLastStep from './FooterLastStep';
import * as Consts from '../../../shared/Consts';

export default class ProfileStep4Confirm extends React.Component {
  render () {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.STEP_4) {
      return null;
    }

    return (
      <>
        <div id='thankyou' className='main_form_info steppers'>
          <div className='title2' style={{ marginBottom: '180px' }}>
          Thank you for the information! You're now on the road to your new home.<div className='title_divider' /><div className='thankyou'>Thank You for your information.<br />We are verifying the info you submitted.</div>
          </div>
          <FooterLastStep
            SaveHandler={() => { this.props.SaveHandler(); }}
            PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.STEP_3); }}
          />
        </div>
      </>
    );
  }
}
