import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as InputHelper from '../Shared/InputHelper';
import * as InputValuesHelper from '../../shared/InputValuesHelper';
import * as Consts from '../../shared/Consts';

export default class OfferBuyerInfo extends React.Component {
  render () {
    const {
      InputValues,
      CurrentTab
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.BuyerFields
    };

    if (CurrentTab !== Consts.BuyerOfferBuyerInfo) {
      return null;
    }

    return (
      <>
        <div className='main_form_info steppers'>
          <div className='title2'>Buyer Information</div>
          <div className='b_texts'>Let's get all the relevant information together and prepare any necessary documents so that they're ready to share with our transaction partners when the time comes.</div>

          <div className='pro_tab'>
            <div className='tab1'>Profile : Buyer One</div>
            <div className='tab_divider desktop' />
            <div className='tab2 desktop'>Buyer One will be the person responsible for completing forms and the first person to sign documents.</div>
          </div>
          <div>
            <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}>
              <span style={{ color: '#111' }}>Note: </span>
              <span>Buyer One is the person responsible for completing forms and, in the case of multiple buyers, the first person to sign documents.</span>
            </div>
          </div>
          <div className='form_inputs'>
            <div className='t_texts'>Official name for Buyer One to be used in all purchase and title documents</div>
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_first_name', TitleUnderInput: true })}
            </div>
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_middle_name', TitleUnderInput: true })}

            </div>
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_last_name', TitleUnderInput: true })}
            </div>
          </div>

          <div className='form_inputs'>
            <div className='t_texts'>Current Mailing Address
              <span className='greyer' style={{ marginLeft: '15px' }}>This is the address to be used on all official documents.</span>
            </div>
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_street', TitleUnderInput: true })}
            </div>
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_city', TitleUnderInput: true })}
            </div>
            <br />
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_state', TitleUnderInput: true })}
            </div>
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_postal_code', TitleUnderInput: true })}
            </div>
          </div>

          <div className='form_inputs'>
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_primary_phone' })}

              <div style={{ margineft: '0' }} className='greyer'>
                <span>This will be the primary contact phone number we will use for anything related to the transaction.</span>
              </div>

            </div>
          </div>

          <div className='form_inputs'>
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_email', ReadOnly: true })}
              <div style={{ marginLeft: 0 }} className='greyer'>
                <span>This is the email address that will be used to communicate updates and send documents for signature.</span>
              </div>
            </div>
          </div>

          <div className='form_inputs radios required required-options'>
            {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_communication_method' })}
          </div>

          <div className='pro_tab alternatess'>
            <div className='tab1'>Additional Buyers</div>
          </div>

          <div className='form_inputs required required-options'>
            <div id='set1'>
              <div className='t_texts'>Will there be an additional buyer?</div>
              <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px', maxWidth: '600px' }}>
                <span>All additional buyers will receive purchase information updates, notifications and be responsibile for signing documentation on your home purchase.</span>
              </div>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_2_active' })}
            </div>

            {InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_2_active') === Consts.OPTION_YES_VALUE &&
              <>
                <div className='reveals animated_fastest' id='info1'>
                  <div className='form_inputs' style={{ marginTop: '50px' }}>
                    <div className='form_title2'>Buyer Two</div>

                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_2_first_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_2_last_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form halfs2 no_bottoms'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_2_email', TitleUnderInput: true })}
                    </div>
                  </div>

                  <div className='form_inputs required required-options' style={{ marginBottom: '50px' }} id='set2'>
                    <div className='t_texts'>Are there any additional buyers?</div>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_3_active' })}
                  </div>
                </div>
              </>}

            {(InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_2_active') === Consts.OPTION_YES_VALUE &&
              InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_3_active') === Consts.OPTION_YES_VALUE) &&
                <div className='reveals animated_fastest' id='info2'>
                  <div className='form_inputs' style={{ marginBottom: '30px' }}>
                    <div className='form_title2'>Buyer Three</div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_3_first_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_3_last_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form halfs2'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_3_email', TitleUnderInput: true })}
                    </div>
                  </div>

                  <div className='form_inputs required required-options' style={{ marginBottom: '50px' }} id='set2'>
                    <div className='t_texts'>Add additional buyer?</div>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_4_active' })}
                  </div>
                </div>}

            {(
              InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_2_active') === Consts.OPTION_YES_VALUE &&
              InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_3_active') === Consts.OPTION_YES_VALUE &&
            InputValuesHelper.GetInputValue(InputValues, 'buyer_info_additional_buyer_4_active') === Consts.OPTION_YES_VALUE) &&
              <div className='reveals animated_fastest' id='info2'>
                <div className='form_inputs' style={{ marginBottom: '0' }}>
                  <div className='form_title2'>Buyer Four</div>

                  <div className='inputer_form thirds'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_4_first_name', TitleUnderInput: true })}
                  </div>
                  <div className='inputer_form thirds'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_4_last_name', TitleUnderInput: true })}
                  </div>
                  <div className='inputer_form halfs2'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_additional_buyer_4_email', TitleUnderInput: true })}
                  </div>
                </div>
              </div>}

            <div className='form_inputs radios' style={{ marginTop: '60px' }}>
              <div className='t_texts' style={{ marginBottom: '5px' }}>
                <span>Would you like to add an Information Recipient?</span>
              </div>
              <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px', maxWidth: '600px' }}>
                <span>Is there someone else you would like to include on communications (for example, a spouse or significant other, attorney, CPA or business advisor) as an Information Recipient? This person will only receive information, they will not be listed as a purchaser.</span>
              </div>

              <div id='set3'>
                <div className='required required-options'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_recipient_active' })}
                </div>
              </div>

              {InputValuesHelper.GetInputValue(InputValues, 'buyer_info_recipient_active') === Consts.OPTION_YES_VALUE &&
                <div className='form_inputs reveals animated_fastest' style={{ marginTop: '50px', marginBottom: '0' }} id='info3'>
                  <div className='form_title2'>Information Recipient</div>
                  <div className='inputer_form thirds'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_recipient_first_name', TitleUnderInput: true })}
                  </div>
                  <div className='inputer_form thirds' style={{ marginBottom: 0 }}>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_recipient_last_name', TitleUnderInput: true })}
                  </div>
                  <div className='inputer_form halfs2' style={{ marginBottom: 0 }}>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_recipient_email', TitleUnderInput: true })}
                  </div>
                </div>}

            </div>
            <div className='pro_tab '>
              <div className='tab1'>Entity Acquisition</div>
            </div>
            <div className='form_inputs m_bottoms'>
              <span>Are you purchasing this property through an entity?</span>
              <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px', maxWidth: '600px' }}>
                <span>An entity is a Trust, LLC or Corporation.</span>
              </div>
              <div id='set4'>
                <div className='required required-options'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_entity_acquisition_active' })}
                </div>
              </div>
            </div>

            {InputValuesHelper.GetInputValue(InputValues, 'buyer_info_entity_acquisition_active') === Consts.OPTION_YES_VALUE &&
              <div className='reveals animated_fastest' id='info4'>
                <div className='form_inputs' style={{ marginBottom: '30px' }}>
                  <div className='t_texts' style={{}}>Entity Information (if applicable)</div>
                  <div className='inputer_form halfs'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_entity_acquisition_entity_name', TitleUnderInput: true })}
                  </div>
                  <div className='inputer_form thirds'>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_entity_acquisition_entity_type', TitleUnderInput: true })}
                  </div>
                </div>
                <div className='form_inputs'>
                  <div className='inputer_form halfs '>
                    {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_info_entity_acquisition_authorized_signer', TitleUnderInput: true })}
                  </div>
                </div>
              </div>}
          </div>
        </div>
        <FooterMiddleStep
          SaveHandler={() => { this.props.SaveHandler(DataSections); }}
          HideSaveButton={this.props.ReadOnly === true}
          PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.BuyerOfferWelcome); }}
          SaveNextHandler={() => { this.props.SaveNextHandler(Consts.BuyerOfferDetails, DataSections); }}
        />
      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_BUYER_INFO
];
