import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class OtherFeatures extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Other' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_611_Additional_Features' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFO_611_Additional_Features', 'ExhaustFans') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_223_Exhaust_Fan_Location' })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFO_223_Exhaust_Fan_Location', 'Other') &&
                    <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_223_t_Text' })}</div>
                }
              </>
          }
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFO_611_Additional_Features', 'AssignedStorageSpace') &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_776_t_Assigned_Storage_Details' })}</div><br />
              </>
          }
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_196_Defects' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFO_196_Defects', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFO_196_t_Text_Explanation' })}</div>
          }
        </div>
      </>
    );
  }
}
