import React from 'react';
import imageArrowW from '../../../images/arrow_w.svg';

export default class BuyerProfileFooter extends React.Component {
  render () {
    const {
      SaveHandler,
      SaveNextHandler
    } = this.props;

    return (
      <>
        <div className='save_btn animated_fastest' onClick={SaveHandler}>Save</div>
        <div className='next_btn animated_fastest' onClick={SaveNextHandler}>Next
          <div className='lm_arrow'><img src={imageArrowW} width='100%' alt='Arrow W' /></div>
        </div>
        <div className='clearer' />
      </>
    );
  }
}
