import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class RegulatedBuildingAndSafety extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='REGULATED BUILDING AND SAFETY FEATURES' />
        <div className='form_inputs'>
          <div className='inputer_form twothirds'>{InputHelper.GenerateInput(
            {
              ...FieldProps,
              FieldName: 'Features_SFR_043_Consent',
              Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFR_043_Consent' })
            })}
          </div>

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFR_x01_Water_Conserving_Plumbing_Fixtures' })}</div>
          <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFR_x01_n' })}</div>

        </div>
      </>
    );
  }
}
