import React from 'react';
import * as Utils from './Utils';

export default function TextAreaInputer (props) {
  const {
    FieldName,
    Required,
    Label,
    MaxLength,
    ValueChangeHandler,
    ValidationResults,
    Type,
    InputValues
  } = props;

  let attributes = {
    name: FieldName,
    type: (Type === undefined ? 'text' : Type)
  };

  if (MaxLength !== undefined) {
    attributes = {
      ...attributes,
      maxLength: MaxLength,
      rows: 5,
      style: { height: 'auto !important' }
    };
  }

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  var className = Utils.GetClassName(ValidationResults, FieldName, Required) + ' form-explanation';

  return (
    <>
      {Label !== '' &&
        <div className='form-explanation-label'>
          {Label}
        </div>}
      <textarea
        className={className}
        onChange={ValueChangeHandler}
        value={value}
        rows={5}
        // cols={20}
        {...attributes}
        disabled={props.ReadOnly === true ? 'disabled' : ''}
      />
      {Utils.GetValidationResult(ValidationResults, FieldName)}
    </>
  );
}
