import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class Utilities extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Utilities' />
        <div className='form_inputs'>
          <div className='inputer_form' style={{ width: '100%' }}>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_285_Water_Source' })}</div>
          <br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFU_285_Water_Source', 'Other') &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_285_008_t_Text_Explanation' })}</div>
                <br />
              </>
          }
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Features_SFU_185_Surcharges_Penalities', ExpFieldName: 'Features_SFU_185_t_Text_Explanation' }} />
          <br />
          <div className='inputer_form halfs' style={{ width: '100%' }}>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_665_Water_Dispose' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFU_665_Water_Dispose', 'Other') &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_665_t_Text_Explanation' })}</div>
                <br />
              </>
          }

          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_213_Is_Gas_Suppiled' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFU_213_Is_Gas_Suppiled', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form halfs' style={{ width: '100%' }}>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_656_How_Gas_Supplied' })}</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFU_820_Is_Gas_Shut_Off_Installed' })}</div>
                <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFU_820_n' })}</div>
              </>
          }

          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Features_SFU_670_Defects', ExpFieldName: 'Features_SFU_670_t_Text_Explanation' }} />
        </div>
      </>
    );
  }
}
