import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class ProfileStep3
  extends React.Component {
  constructor () {
    super();
    this.listPriceRef = React.createRef();
  }

  render () {
    const {
      InputValues,
      CurrentTab,
      ClosingCosts
    } = this.props;
    const handleValueChange = this.props.ValueChangeHandler;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    if (CurrentTab !== Consts.STEP_3) {
      return null;
    }

    var listPrice = InputValuesHelper.GetInputValue(InputValues, 'Listing_LMF_432_List_Price');
    if (listPrice === undefined) {
      listPrice = 0;
    }

    var ListFee = '-';
    var IncentiveCredit = '-';
    // var BuyerAgentFee = '-';

    // var CityTransferTaxRate = '-';
    var CityTransferTax = '-';
    // var CountyTransferTaxRate = '-';
    var CountyTransferTax = '-';

    var EscrowFee = '-';
    var TitleFee = '-';

    var NaturalHazardReport = '-';
    var RegulationCompliance = '-';

    var HomeWarranty = '-';

    var SurpassNet = '-';
    // var SurpassSave = '-';
    var AdvantageNet = '-';
    // var AdvantageSave = '-';

    if (ClosingCosts !== undefined) {
      ListFee = ClosingCosts.BrokerFees.ListFee;
      IncentiveCredit = ClosingCosts.BrokerFees.IncentiveCredit;
      // BuyerAgentFee = ClosingCosts.BrokerFees.BuyerAgentFee;

      // CityTransferTaxRate = ClosingCosts.Taxes.CityTransferTaxRate;
      CityTransferTax = ClosingCosts.Taxes.CityTransferTax;
      // CountyTransferTaxRate = ClosingCosts.Taxes.CountyTransferTaxRate;
      CountyTransferTax = ClosingCosts.Taxes.CountyTransferTax;

      EscrowFee = ClosingCosts.ClosingCosts.EscrowFee;
      TitleFee = ClosingCosts.ClosingCosts.TitleFee;

      NaturalHazardReport = ClosingCosts.RegulatoryRequirements.NaturalHazardReport;
      RegulationCompliance = ClosingCosts.RegulatoryRequirements.RegulationCompliance;

      HomeWarranty = ClosingCosts.Other.HomeWarranty;

      SurpassNet = ClosingCosts.SurpassNet;
      // SurpassSave = ClosingCosts.SurpassSave;
      AdvantageNet = ClosingCosts.AdvantageNet;
      // AdvantageSave = ClosingCosts.AdvantageSave;
    }

    return (
      <>

        <div id='step3' className='main_form_info steppers'>
          <div className='title2'><div className='number_title'>03</div> Listing</div>

          <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Listing_LMF_811_Label' })}</div>

          <div className='pro_tab'>
            <div className='tab1'>Listing Details</div>
            <div className='tab_divider desktop' />
            <div className='tab2 desktop'>We use human intelligence and machine learning to determine value.</div>
          </div>
          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}><span style={{ color: '#111' }}>Note:</span> We use human intelligence and machine learning to determine value.</div>

          <div className='form_inputs'>
            <div className='t_texts'>Choose Your List Price</div>
            <div className='container'>
              <div className='range-div'>
                <input type='range' name='Listing_LMF_432_List_Price' onChange={handleValueChange} className='range' min='30000' max='500000' step='1' value={listPrice} ref={this.listPriceRef} />
                <label htmlFor='Listing_LMF_432_List_Price' className='list-price-label'>{`${listPrice} USD`}</label>
                {/* <span className='range-thumb' style={position}>{`${listPrice} USD`}</span> */}
                <div className='box-minmax'>30,000</div>
                <div className='box-minmax' style={{ float: 'right' }}>500,000</div>
                <div className='clearer' />
              </div>
            </div>
          </div>

          <div className='pro_tab alternatess'>
            <div className='tab1'>Loan Payoff</div>
          </div>

          <div className='form_inputs'>
            <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMF_088_Total_Loans' })}</div>
            <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Listing_LMF_088_Label' })}</div>
          </div>

          <div className='pro_tab alternatess'>
            <div className='tab1'>Closing Costs</div><div className='tab_divider desktop' /><div className='tab2 desktop'>Estimated Closing Costs.</div>
          </div>
          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}><span style={{ color: '#111' }}>Note:</span> Estimated Closing Costs.</div>

          <div className='b_texts' style={{ color: '#111', marginBottom: '120px' }}><div className='form_title2'>Your Listing Price:</div>
            <div className='grey2' style={{ marginLeft: '0' }}>Nile List Fee: <span className='darker'>{ListFee}</span><br />

              <span>Buyer Incentive Credit: </span><span className='darker'>{IncentiveCredit}</span><br />
              <span>Total Closing Costs: </span><span className='darker'>TBD</span><br />
              <br />
              <div className='form_title2'>Your Net Proceeds:</div>

              <span className='darker'>{SurpassNet} (nile Preferred)<br />{AdvantageNet} (Nile Collbaoration)
              </span>

              <div className='costdivider' />
              <div className='form_title2'>Closing Cost Breakdown:</div>
              <span>Seller Escrow Fees: </span><span className='darker'>{EscrowFee}</span><br />
              <span>Seller Title Policy: </span><span className='darker'>{TitleFee}</span><br />
              <span>County Transfer Tax: </span><span className='darker'>{CountyTransferTax}</span><br />
              <span>City Transfer Tax: </span><span className='darker'>{CityTransferTax}</span><br />
              <span>HOA Transfer Fee: </span><span className='darker'>TBD</span><br />
              <span>HOA Documents (per HOA): </span><span className='darker'>TBD</span><br />
              <span>1 Year Home Warranty: </span><span className='darker'>{HomeWarranty}</span><br /><br />

              <div className='form_title2'>Reports: </div>
              <span>Natural Hazard Disclosure: </span><span className='darker'>{NaturalHazardReport}</span><br />
              <span>Government Safety and Compliance: </span><span className='darker'>{RegulationCompliance}</span>
            </div>

          </div>

          <FooterMiddleStep
            SaveHandler={() => { this.props.SaveHandler(DataSections); }}
            PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.STEP_2); }}
            SaveNextHandler={() => { this.props.SaveNextHandler(Consts.STEP_4, DataSections); }}
          />

        </div>

      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_SELLER_LMF
];
