import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';

export default class Nuissance extends React.Component {
  render () {
    const {
      FieldProps
    } = this.props;

    const mainFieldName = 'primary_questionnaire_pqn_076';
    const itemsFieldName = 'primary_questionnaire_pqn_076_items';
    const explanationFieldName = 'primary_questionnaire_pqn_076_other_explanation';
    const InputValues = FieldProps.InputValues;

    const displayItems = InputValuesHelper.GetInputValue(InputValues, mainFieldName) === Consts.OPTION_YES_VALUE;
    var displayExplanation = false;
    if (displayItems) {
      const explanationField = FieldProps.FieldDefs.find(field => field.FieldName === explanationFieldName);
      if (explanationField !== undefined) {
        var dependsOnFieldName = explanationField.DependsOn;
        var dependsOnValue = explanationField.DependsOnValue;

        const selectedItems = InputValuesHelper.GetInputValue(InputValues, dependsOnFieldName);
        if (selectedItems !== undefined && selectedItems.find(selectedItem => selectedItem.value === dependsOnValue)) {
          displayExplanation = true;
        }
      }
    }

    return (
      <div className='form_inputs'>
        {/* <div className='t_texts'>
          {InputHelper.GetFieldTitle({ ...FieldProps, FieldName: mainFieldName })}
        </div> */}
        <div className='inputer_form halfs'>
          {InputHelper.GenerateInput({ ...FieldProps, FieldName: mainFieldName })}
        </div>

        {
          displayItems &&
            <div className='inputer_form'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: itemsFieldName })}
            </div>
        }

        {
          displayExplanation &&
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: explanationFieldName })}
            </div>
        }
      </div>
    );
  }
}
