import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Pool extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Pool' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_536_Is_There_A_Pool_Spa' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_536_Is_There_A_Pool_Spa', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_536_x1_Pool_Spa', Label: undefined })}</div><br />
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_536_x1_Pool_Spa', 'Pool') &&
                    <>
                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_977_Is_The_Pool_Heated' })}</div><br />
                      {
                        InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_977_Is_The_Pool_Heated', Consts.OPTION_YES_VALUE) &&
                          <>
                            <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_977_x1_Pool_Heat_Type' })}</div><br />
                          </>
                      }

                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_817_Defects' })}</div><br />
                      {
                        InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_817_Defects', Consts.OPTION_YES_VALUE) &&
                          <>
                            <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_817_t_Explanation' })}</div><br />
                          </>
                      }

                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_230_Pool_Safety_Features' })}</div><br />
                    </>
                }

                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_536_x1_Pool_Spa', 'Spa') &&
                    <>
                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_934_Is_The_Spa_Heated' })}</div><br />
                      {
                        InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_934_Is_The_Spa_Heated', Consts.OPTION_YES_VALUE) &&
                          <>
                            <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_934_x1_Spa_Heat_Type' })}</div><br />
                          </>
                      }
                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_230_Safety_Features' })}</div><br />
                      <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFP_230_740' })}</div><br />
                      <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_183_Defects' })}</div><br />
                      {
                        InputValuesHelper.HasInputValue(InputValues, 'Features_SFP_183_Defects', Consts.OPTION_YES_VALUE) &&
                          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFP_183_t_Explanation' })}</div>
                      }

                    </>

                }
              </>
          }
        </div>
      </>
    );
  }
}
