import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class OperatingCosts extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Operating Costs' />
        <div className='form_inputs'>
          <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Features_SFC_582' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFC_999_Gas' })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFC_060_Electric' })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFC_042_Water' })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFC_637_Other_Maintenance' })}</div>
        </div>
      </>
    );
  }
}
