import React from 'react';
import * as Consts from '../../../shared/Consts';

export default class Navigator extends React.Component {
  render () {
    const stepNavClasses = 'step_btn animated_fastest';
    const stepNavActiveClasses = 'step_btn animated_fastest step_active';
    const {
      CurrentTab
      // onStepNavClick
    } = this.props;

    return (
      <>
        <div className='form_title'>Welcome to NILE</div>
        <div className='stickit'>
          <div className='steps'>
            <div id={Consts.STEP_1} className={CurrentTab === Consts.STEP_1 ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />1
              <div className='down_border' />
            </div>
            <div id={Consts.STEP_2} className={CurrentTab === Consts.STEP_2 ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />2
              <div className='down_border' />
            </div>
            <div id={Consts.STEP_3} className={(CurrentTab === Consts.STEP_3 || CurrentTab === Consts.STEP_3_5 || CurrentTab === Consts.STEP_3_9) ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />3
              <div className='down_border' />
            </div>
            <div id={Consts.STEP_4} className={CurrentTab === Consts.STEP_4 ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />4
              <div className='down_border' />
            </div>
          </div>
        </div>
      </>
    );
  }
}
