import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class HeatingCooling extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Heating & Cooling' />
        <div className='form_inputs'>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_079_Please_Select_The_Heating_Method' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_805_Cooling_Method' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_977_Fireplace' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFH_977_Fireplace', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_977_t_Fireplace_Number' })}</div><br />
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_358_Fireplace_Location' })}</div><br />
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_924_Features' })}</div><br />
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_538_Chimney' })}</div><br />
              </>
          }

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_218_Heating_Cooling_Features' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFH_218_Heating_Cooling_Features', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFH_218_t_Text_Explanation' })}</div>
          }

        </div>
      </>
    );
  }
}
