import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class TitleMatters extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='Title Matters' />
        <div className='form_inputs'>
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTT_086_Pace_Lien', ExpFieldName: 'Finance_FTT_086_T_Text_Explanation' }} />

          <div className='t_texts'>Upload files</div>
          {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTT_086-u_Upload' })}

          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTT_463_Other_Loans', ExpFieldName: 'Finance_FTT_463_T_Explaination' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTT_895_Others_Matters', ExpFieldName: 'Finance_FTT_895_T_Text_Explanation' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTT_009_Other_Matters', ExpFieldName: 'Finance_FTT_009_T_Text_Explanation' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTT_620_Other_Matters', ExpFieldName: 'Finance_FTT_620_T_Text_Explanation' }} />
        </div>
      </>
    );
  }
}
