import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as InputHelper from '../Shared/InputHelper';
import * as Consts from '../../shared/Consts';
import * as InputValuesHelper from '../../shared/InputValuesHelper';

export default class ProfileStep6 extends React.Component {
  render () {
    const {
      InputValues,
      CurrentTab
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.BuyerFields
    };

    if (CurrentTab !== Consts.STEP_6) {
      return null;
    }

    var totalAmount = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_total_amount');
    var dueDate = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_due_date');
    var financialInstitution = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_financial_institution');
    var backAddress = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_bank_address');
    var routingNumber = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_routing_number');
    var swiftCode = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_swift_code');
    var accountNumber = InputValuesHelper.GetInputValue(InputValues, 'buyer_info_wire_info_account_number');
    totalAmount = (totalAmount === undefined ? 'TBD' : totalAmount);
    dueDate = (dueDate === undefined ? 'TBD' : dueDate);
    financialInstitution = (financialInstitution === undefined ? 'TBD' : financialInstitution);
    backAddress = (backAddress === undefined ? 'TBD' : backAddress);
    routingNumber = (routingNumber === undefined ? 'TBD' : routingNumber);
    swiftCode = (swiftCode === undefined ? 'TBD' : swiftCode);
    accountNumber = (accountNumber === undefined ? 'TBD' : accountNumber);

    const wireInitiated = InputValuesHelper.HasInputValue(InputValues, 'buyer_info_wire_intitated', Consts.OPTION_YES_VALUE);
    console.log(wireInitiated);

    return (
      <div className='main_form_info steppers'>
        <div className='title2'><div className='number_title'>06</div> Congratulations on the purchase of your new home.  </div>
        <div className='pro_tab'>
          <div className='tab1'>Wire Instructions</div>
          <div className='tab_divider desktop' />
          <div className='tab2 desktop'>Below Please Find Wire Instructions. Your wire in the amount of {totalAmount} is due by {dueDate}.</div>
        </div>
        <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}>
          <span style={{ color: '#111' }}>Note:</span> Below Please Find Wire Instructions. Your wire in the amount of {totalAmount} is due by {dueDate}.
        </div>
        <div className='form_inputs'>
          <div className='bullet_list'>
            {PaymentInfoRow('Financial Institution', financialInstitution)}
            {PaymentInfoRow('Bank Address', backAddress)}
            {PaymentInfoRow('Routing Number', routingNumber)}
            {PaymentInfoRow('SWIFT CODE (INTL)', swiftCode)}
            {PaymentInfoRow('Account Number', accountNumber)}
          </div>
        </div>
        <div className='d_line' />
        <div className='form_inputs morebottom'>
          {InputHelper.GenerateInput({
            ...FieldProps,
            FieldName: 'buyer_info_wire_intitated',
            Label: 'Please Check Here once you have intitated the wire so we can be on the lookout.'
          })}
        </div>
        <FooterMiddleStep
          SaveHandler={() => { this.props.SaveHandler(DataSections); }}
          PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.STEP_5); }}
          SaveNextHandler={() => { this.props.SaveNextHandler(Consts.STEP_7, DataSections); }}
          SaveNextDisabled={!wireInitiated}
        />
      </div>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_BUYER_WIRE_STATUS
];

const PaymentInfoRow = (title, content) => {
  return (
    <div className='b_item'>
      <div className='bullet'>
        <div className='bull'>{title}</div>
      </div>
      <div className='bull2'>
        <div className='bullet2'>{content}</div>
      </div>
    </div>
  );
};
