import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class TitleHolderCorporate extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Corporate Title Holder' />
        <div className='form_inputs'>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTO_355_Corporation_Tax_Id_Number', Label: undefined })}</div>
          <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTO_355-n' })}</div>
          <div className='t_texts'>Upload Corporate Documents</div>
          {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTO_355-u_Upload_Corporate_Documents' })}
        </div>
      </>
    );
  }
}
