import React from 'react';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { Auth } from 'aws-amplify';
import Navigator from '../../components/SellerProperty/GeneralInfo/Navigator';
import ActionsBar from '../../components/SellerProperty/GeneralInfo/ActionsBar';
import GeneralInfo from '../../components/SellerProperty/GeneralInfo/GeneralInfo';
import axios from 'axios';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import * as Consts from '../../shared/Consts';
import * as Validator from '../../shared/Validator';
import * as FieldDef from '../../shared/FieldDefinition/SellerGeneralInfo';
import * as PostDataGenerator from '../../shared/PostDataGenerator';
import * as ResponseDataConvert from '../../shared/ResponseDataConvert';
import { toastr } from 'react-redux-toastr';

class GeneralInfoDashboard extends React.Component {
  constructor (props) {
    super(props);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.SaveHandler = this.SaveHandler.bind(this);

    if (props.match !== undefined) {
      var startStep = (props.match.params.step === undefined ? Consts.STEP_1 : props.match.params.step);
    }

    this.state = {
      propertyId: props.match.params.propertyId,
      Loading: false,
      SelectedTab: startStep,
      InputValues: {},
      InputValuesChanged: {},
      ValidationResults: undefined
    };
  }

  async componentDidMount () {
    try {
      this.setState({ Loading: true, InputValuesChanged: {} });
      const requestConfig = {
        headers: {
          'Content-Type': Consts.HEADER_APPLICATION_JSON,
          Authorization: (await Auth.currentSession()).getIdToken().getJwtToken()
        }
      };

      const includes = [
        Consts.DATA_SECTION_SELLER_GIO,
        Consts.DATA_SECTION_SELLER_GIT
      ];

      const url = `${Consts.API_SELLER_PRIMARY_QUESTIONNAIRE}/${this.state.propertyId}/${await PostDataGenerator.GetUserId(Auth)}/?include=${includes.join(',')}`;
      const response = await axios.get(url, requestConfig);

      let inputValues = this.state.InputValues;
      if (inputValues === undefined) {
        inputValues = {};
      }

      var fieldDefs = FieldDef.GeneralInfoFields;
      includes.forEach(dataSection => {
        const result = this.ConvertData(dataSection, fieldDefs, response.data);
        inputValues = { ...inputValues, ...result };
      });

      this.setState({ InputValues: inputValues, Loading: false });
    } catch (error) {
      console.log(error);
      this.setState({ Loading: false });
      toastr.error('Load general information', 'Please refresh page and try again');
    }
  }

  ConvertData (dataSection, fieldDefs, responseData) {
    const sectionData = responseData.PrimaryQuestionnaire[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(sectionData, fieldDefs, dataSection);
    return sectionDataResult;
  }

  async SaveHandler (event) {
    await this.SaveData(event, false, true);
  }

  async SaveData (event, gotoNextOnSucceeded, displaySucceededMessage) {
    try {
      this.setState({ Loading: true });
      const fieldsDefs = FieldDef.GeneralInfoFields;
      const validationResults = Validator.Validate(this.state.InputValues, fieldsDefs);
      this.setState({ ValidationResults: validationResults });

      if (validationResults === undefined || validationResults.length === 0) {
        const sections = [
          Consts.DATA_SECTION_SELLER_GIO,
          Consts.DATA_SECTION_SELLER_GIT
        ];

        const postData = {};
        sections.forEach(section => {
          postData[section] = PostDataGenerator.GetPostData(this.state.InputValues, fieldsDefs, section);
        });

        const body = {
          PropertyId: this.state.propertyId,
          UserId: await PostDataGenerator.GetUserId(Auth),
          Data: postData
        };
        const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
        const response = await axios.post(Consts.API_SELLER_PRIMARY_QUESTIONNAIRE, body, requestConfig);

        var putFileErrors = [];
        if (postData[Consts.DATA_SECTION_SELLER_GIT] !== undefined && postData[Consts.DATA_SECTION_SELLER_GIT].GIT_026_lease_docs !== undefined) {
          const files = postData[Consts.DATA_SECTION_SELLER_GIT].GIT_026_lease_docs;

          // upload files
          for (let i = 0; i < response.data.urls.length; i++) {
            const urlItem = response.data.urls[i];
            const file = files.find(item => item.id === urlItem.id);
            if (file !== undefined) {
              var options = {
                headers: {
                  'Content-Type': file.type
                }
              };
              try {
                const fileToUpload = file.file;
                await axios.put(urlItem.url, fileToUpload, options);
              } catch (error) {
                putFileErrors.push(file.name);
                toastr.error('Save general information', `Failed to upload file ${file.name}`);
              }
            }
          }
        }

        if (putFileErrors.length === 0) {
          toastr.success('Save general information', 'Succeeded');
        }
      }
    } catch (error) {
      toastr.error('Save general information', 'Please refresh page and try again');
    }
    this.setState({ Loading: false });
  }

  ValueChangeHandler (event, fieldName, fieldType, value) {
    const inputValues = this.state.InputValues;

    if (fieldName !== undefined && fieldType !== undefined) {
      inputValues[fieldName] = value;
    } else {
      if (event.target.type === 'text') {
        inputValues[event.target.name] = event.target.value;
      } else if (event.target.type === 'checkbox') {
        if (event.target.dataset === undefined) {
          inputValues[event.target.name] = event.target.checked;
        } else if (event.target.dataset.type === 'multiple-choises') {
          const label = event.target.dataset.label;
          const value = event.target.dataset.value;

          var currentSelectedItems = inputValues[event.target.name];
          if (currentSelectedItems === undefined) {
            currentSelectedItems = [];
          } else {
            currentSelectedItems = currentSelectedItems.filter(item => item.value !== value);
          }
          if (event.target.checked) {
            currentSelectedItems.push({ label, value });
          }

          inputValues[event.target.name] = currentSelectedItems;
        } else {
          inputValues[event.target.name] = event.target.checked;
        }
      } else if (event.target.type === 'date') {
        inputValues[event.target.name] = event.target.value;
      } else {
        inputValues[event.target.name] = event.target.value;
      }
    }
    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputValuesChanged (step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  render () {
    const stepProps = {
      SaveHandler: this.SaveHandler,
      // SaveNextHandler: this.SaveNextHandler,
      // PreviousStepHandler: this.PreviousStepHandler,
      ValueChangeHandler: this.ValueChangeHandler,
      ValidationResults: this.state.ValidationResults,
      InputValues: this.state.InputValues
    };

    return (
      <div>
        <BlockUi
          tag='div'
          blocking={this.state.Loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <div className='container'>
            <form action='#' method='POST' data-name='buyer-form' id='buyer-form' encType='multipart/form-data'>
              <Navigator />
              <GeneralInfo
                {...stepProps}
              />
              <ActionsBar {...stepProps} />
            </form>
          </div>
        </BlockUi>
      </div>
    );
  }
}

export default withCustomAuthenticator(GeneralInfoDashboard);
