import 'react-app-polyfill/ie9';
import React from 'react';
import qs from 'query-string';
import { Auth } from 'aws-amplify';
import OfferNotFound from '../../components/BuyerProfile/OfferNotFound';
import OfferWelcome from '../../components/BuyerProfile/OfferWelcome';
import OfferBuyerInfo from '../../components/BuyerProfile/OfferBuyerInfo';
import OfferDetails from '../../components/BuyerProfile/OfferDetails';
import OfferVerifications from '../../components/BuyerProfile/OfferVerifications';
import OfferSubmission from '../../components/BuyerProfile/OfferSubmission';
import ProfileStep5 from '../../components/BuyerProfile/ProfileStep5';
import ProfileStep6 from '../../components/BuyerProfile/ProfileStep6';
import ProfileStep7 from '../../components/BuyerProfile/ProfileStep7';
import ProfileStep8 from '../../components/BuyerProfile/ProfileStep8';
import ProfileStep9 from '../../components/BuyerProfile/ProfileStep9';
import BuyerProfileNavigator from '../../components/BuyerProfile/BuyerProfileNavigator';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import axios from 'axios';
import * as Consts from '../../shared/Consts';
import * as Validator from '../../shared/Validator';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as PostDataGenerator from '../../shared/PostDataGenerator';
import * as ResponseDataConvert from '../../shared/ResponseDataConvert';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

class BuyerProfileDashboard extends React.Component {
  constructor (props) {
    super();
    this.SaveHandler = this.SaveHandler.bind(this);
    this.SaveNextHandler = this.SaveNextHandler.bind(this);
    this.SubmitOfferHandler = this.SubmitOfferHandler.bind(this);
    this.handleStepNavClick = this.handleStepNavClick.bind(this);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.handleGoToStep = this.handleGoToStep.bind(this);
    this.SubmitContactUs = this.SubmitContactUs.bind(this);

    const queryString = qs.parse(props.location.search, { ignoreQueryPrefix: true });
    const offerId = queryString.offerid;
    const startStep = queryString.step === undefined ? Consts.BuyerOfferWelcome : queryString.step;

    this.state = {
      offerId: offerId,
      token: '',
      SelectedTab: startStep,
      InputValues: {},
      OfferData: undefined,
      InputValuesChanged: {},
      ValidationResults: undefined,
      Loading: false,
      NotFound: false,
      Submitted: false
    };
  }

  async componentDidMount () {
    if (this.state.offerId === undefined) {
      this.setState({ Loading: false, OfferData: undefined, NotFound: true });
      return;
    }

    try {
      this.setState({ Loading: true, InputValuesChanged: {} });
      const requestConfig = {
        headers: {
          'Content-Type': Consts.HEADER_APPLICATION_JSON,
          Authorization: (await Auth.currentSession()).getIdToken().getJwtToken()
        }
      };

      const includes = [
        Consts.DATA_SECTION_BUYER_INFO,
        Consts.DATA_SECTION_BUYER_INIT_OFFER,
        Consts.DATA_SECTION_BUYER_FINANCIAL_VERIFICATIONS,
        Consts.DATA_SECTION_BUYER_WIRE_INFO,
        Consts.DATA_SECTION_BUYER_WIRE_STATUS,
        Consts.DATA_SECTION_BUYER_INSPECTIONS,
        Consts.DATA_SECTION_BUYER_STATEMENT_OF_INFORMATION,
        Consts.DATA_SECTION_BUYER_OFFER,
        Consts.DATA_SECTION_BUYER_OFFER_CONFIRM
      ];

      const url = `${Consts.API_BUYER_OFFER}/${this.state.offerId}?include=${includes.join(',')}`;
      const response = await axios.get(url, requestConfig);

      let inputValues = this.state.InputValues;
      if (inputValues === undefined) {
        inputValues = {};
      }

      var fieldDefs = FieldDef.BuyerFields;

      includes.forEach(dataSection => {
        const result = this.ConvertData(dataSection, fieldDefs, response.data);
        inputValues = { ...inputValues, ...result };
      });

      this.setState({ InputValues: inputValues, OfferData: response.data.Offer, Loading: false, NotFound: false, Submitted: response.data.Offer.Submitted });
    } catch (error) {
      console.log(error);
      if (error.response.status === 404) {
        this.setState({ Loading: false, OfferData: undefined, NotFound: true });
      } else {
        this.setState({ Loading: false, OfferData: undefined });
        toastr.error('Load buyer profile', 'Please refresh page and try again');
      }
    }
  }

  async SubmitContactUs (message) {
    var result;
    try {
      const body = {
        Message: message
      };
      const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
      await axios.post(`${Consts.API_BUYER_OFFER}/${this.state.offerId}/contactus`, body, requestConfig);
      toastr.success('Contact US', 'You request has been submitted.');
      result = true;
    } catch (error) {
      result = false;
      console.log(error);
      var errorMessage = 'Please refresh page and try again';
      if (error.response.status === 404) {
        if (error.response.data !== undefined && error.response.data.Message !== undefined) {
          errorMessage = error.response.data.Message;
        }
      }
      toastr.error('Failed to submit your message', errorMessage);
    }
    return result;
  }

  async SubmitOfferHandler (nextStep, dataSections) {
    const result = await this.SaveData(false, dataSections);

    if (result === true) {
      this.setState({ Loading: true });
      try {
        const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
        await axios.patch(`${Consts.API_BUYER_OFFER}/${this.state.offerId}/submit`, undefined, requestConfig);

        this.handleGoToStep(nextStep);
      } catch (error) {
        console.log(error);
        var errorMessage = 'Please refresh page and try again';
        if (error.response.status === 409) {
          if (error.response.data !== undefined && error.response.data.Message !== undefined) {
            errorMessage = error.response.data.Message;
          }
        }
        toastr.error('Failed to submit offer', errorMessage);
      }
      this.setState({ Loading: false });
    }
  }

  async SaveNextHandler (nextStep, dataSections) {
    const result = await this.SaveData(false, dataSections);
    if (result) {
      this.handleGoToStep(nextStep);
    }
  }

  async SaveHandler (dataSections) {
    await this.SaveData(true, dataSections);
  }

  async SaveData (displaySucceededMessage, dataSections) {
    if (dataSections === undefined || dataSections.length === 0) {
      if (displaySucceededMessage) {
        toastr.success('Save buyer profile', 'Succeeded');
      }
      return true;
    }

    const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
    this.setState({ ValidationResults: validationResults });
    if (validationResults !== undefined && validationResults.length > 0) {
      return false;
    }

    this.setState({ Loading: true });
    const section = this.state.SelectedTab;

    try {
      if (section === Consts.BuyerOfferBuyerInfo) {
        await this.SaveBuyerInfo();
        this.UpdateInputValuesChanged(Consts.BuyerOfferBuyerInfo, false);
      }

      if (section === Consts.BuyerOfferDetails) {
        await this.SaveBuyerOfferDetails();
        this.UpdateInputValuesChanged(Consts.BuyerOfferDetails, false);
      }

      if (section === Consts.BuyerOfferVerifications) {
        await this.SaveVerifications();
        this.UpdateInputValuesChanged(Consts.BuyerOfferVerifications, false);
      }

      if (section === Consts.STEP_6) {
        await this.SaveStep6Data();
        this.UpdateInputValuesChanged(Consts.STEP_6, false);
      }

      if (section === Consts.STEP_7) {
        await this.SaveStep7Data();
        this.UpdateInputValuesChanged(Consts.STEP_7, false);
      }

      if (section === Consts.STEP_8) {
        await this.SaveStep8Data();
        this.UpdateInputValuesChanged(Consts.STEP_8, false);
      }

      if (displaySucceededMessage === true) {
        toastr.success('Save buyer profile', 'Succeeded');
      }
      this.setState({ Loading: false });
      return true;
    } catch (Error) {
      console.log(Error);
      toastr.error('Save buyer profile', 'Please refresh page and try again');
      this.setState({ Loading: false });
      return false;
    }
  }

  async SaveBuyerInfo () {
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.BuyerOfferBuyerInfo] === undefined || this.state.InputValuesChanged[Consts.BuyerOfferBuyerInfo] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;

    await this.SubmitData(Consts.DATA_SECTION_BUYER_INFO, fieldDefs);
  }

  async SaveBuyerOfferDetails () {
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.BuyerOfferDetails] === undefined || this.state.InputValuesChanged[Consts.BuyerOfferDetails] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;

    await this.SubmitData(Consts.DATA_SECTION_BUYER_INIT_OFFER, fieldDefs);
  }

  async SaveVerifications () {
    const putFileErrors = [];
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.BuyerOfferVerifications] === undefined || this.state.InputValuesChanged[Consts.BuyerOfferVerifications] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;
    const postData = PostDataGenerator.GetPostData(this.state.InputValues, fieldDefs, Consts.DATA_SECTION_BUYER_FINANCIAL_VERIFICATIONS);

    // override post data
    if (postData.LoanNeeded === true) {
      postData.VerificationOfFunds = [];

      if (postData.PreApproved === true) {
        postData.ConnectToLender = undefined;
        postData.LendingInstitution = undefined;
        postData.LoanOfficerFirstName = undefined;
        postData.LoanOfficerLastName = undefined;
        postData.Email = undefined;
        postData.Phone = undefined;
      } else {
        postData.PreApprovalLetter = [];
        postData.DownPaymentVerificationOfFunds = [];
      }
    } else {
      postData.PreApproved = undefined;
      postData.ConnectToLender = undefined;
      postData.LendingInstitution = undefined;
      postData.LoanOfficerFirstName = undefined;
      postData.LoanOfficerLastName = undefined;
      postData.Email = undefined;
      postData.Phone = undefined;
      postData.PreApprovalLetter = [];
      postData.DownPaymentVerificationOfFunds = [];
    }

    const body = {
      Type: Consts.DATA_SECTION_BUYER_FINANCIAL_VERIFICATIONS,
      Data: postData
    };
    const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
    const response = await axios.patch(`${Consts.API_BUYER_OFFER}/${this.state.offerId}`, body, requestConfig);

    const files = postData.PreApprovalLetter.concat(postData.DownPaymentVerificationOfFunds).concat(postData.VerificationOfFunds);
    // upload files
    for (let i = 0; i < response.data.urls.length; i++) {
      const urlItem = response.data.urls[i];
      const file = files.find(item => item.id === urlItem.id);
      if (file !== undefined) {
        var options = {
          headers: {
            'Content-Type': file.type
          }
        };
        try {
          const fileToUpload = file.file;
          await axios.put(urlItem.url, fileToUpload, options);
        } catch (error) {
          putFileErrors.push(file.name);
        }
      }
    }

    if (putFileErrors.length > 0) {
      throw Error('Error in uploading files for Verifications');
    }
  }

  async SaveStep6Data () {
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.STEP_6] === undefined ||
      this.state.InputValuesChanged[Consts.STEP_6] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;
    await this.SubmitData(Consts.DATA_SECTION_BUYER_WIRE_STATUS, fieldDefs);
  }

  async SaveStep7Data () {
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.STEP_7] === undefined ||
      this.state.InputValuesChanged[Consts.STEP_7] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;
    await this.SubmitData(Consts.DATA_SECTION_BUYER_INSPECTIONS, fieldDefs);
  }

  async SaveStep8Data () {
    // skip if data not changed
    if (this.state.InputValuesChanged[Consts.STEP_8] === undefined ||
      this.state.InputValuesChanged[Consts.STEP_8] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.BuyerFields;
    await this.SubmitData(Consts.DATA_SECTION_BUYER_STATEMENT_OF_INFORMATION, fieldDefs);
  }

  async SubmitData (dataSection, fieldDefs) {
    const postData = PostDataGenerator.GetPostData(this.state.InputValues, fieldDefs, dataSection);
    const body = {
      Type: dataSection,
      Data: postData
    };

    const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
    await axios.patch(`${Consts.API_BUYER_OFFER}/${this.state.offerId}`, body, requestConfig);
  }

  ConvertData (dataSection, fieldDefs, responseData) {
    const sectionData = responseData.Offer[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(sectionData, fieldDefs, dataSection);
    return sectionDataResult;
  }

  ValueChangeHandler (event, fieldName, fieldType, value) {
    const inputValues = this.state.InputValues;

    var targetFieldName;
    var targetValue;
    if (fieldName !== undefined && fieldType !== undefined) {
      targetFieldName = fieldName;
      targetValue = value;
      inputValues[fieldName] = value;
    } else {
      targetValue = event.target.value;
      inputValues[event.target.name] = targetValue;
    }

    if (targetFieldName === 'buyer_info_inspection_plan') {
      if (targetValue === 'basic') {
        inputValues.buyer_info_inspections_fee = 500;
      } else if (targetValue === 'recommended') {
        inputValues.buyer_info_inspections_fee = 1500;
      } else {
        inputValues.buyer_info_inspections_fee = 0;
      }
    }

    if (targetValue === Consts.OPTION_NO_VALUE) {
      if (targetFieldName === 'buyer_info_additional_buyer_2_active') {
        inputValues.buyer_info_additional_buyer_3_active = Consts.OPTION_NO_VALUE;
        inputValues.buyer_info_additional_buyer_4_active = Consts.OPTION_NO_VALUE;
      } else if (targetFieldName === 'buyer_info_additional_buyer_3_active') {
        inputValues.buyer_info_additional_buyer_4_active = Consts.OPTION_NO_VALUE;
      } else if (targetFieldName === 'buyer_init_offer_home_sale_contingency') {
        inputValues.buyer_init_offer_sale_property_status = undefined;
      }
    }

    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputValuesChanged (step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  handleStepNavClick (event, dataSections) {
    if (this.state.SelectedTab === Consts.BuyerOfferBuyerInfo || this.state.SelectedTab === Consts.STEP_3) {
      const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
      this.setState({ ValidationResults: validationResults });
      if (validationResults === undefined || validationResults.length === 0) {
        this.setState({ SelectedTab: event.target.id });
      }
    } else {
      this.setState({ SelectedTab: event.target.id });
    }
  }

  ValidateInputValue (dataSections, inputValues) {
    const fieldDefs = FieldDef.BuyerFields;
    var validateFields = [];
    if (dataSections !== undefined) {
      dataSections.forEach(dataSection => {
        const dataFields = fieldDefs.filter(field => field.DataSection === dataSection);
        dataFields.forEach(dataField => {
          validateFields.push(dataField);
        });
      });
    }

    return Validator.Validate(inputValues, validateFields);
  }

  handleGoToStep (step) {
    this.setState({ SelectedTab: step });
    window.scrollTo(0, 0);
  }

  render () {
    var offerPart1ReadOnly = this.state.Submitted;
    const stepProps = {
      CurrentTab: this.state.SelectedTab,
      SaveHandler: this.SaveHandler,
      SaveNextHandler: this.SaveNextHandler,
      ValueChangeHandler: this.ValueChangeHandler,
      GoToStepHandler: this.handleGoToStep,
      ValidationResults: this.state.ValidationResults,
      InputValues: this.state.InputValues,
      OfferData: this.state.OfferData,
      SubmitContactUsHandler: this.SubmitContactUs
    };

    const offerPart1Props = { ...stepProps, ReadOnly: offerPart1ReadOnly };
    const offerVerificationsProps = { ...offerPart1Props, SaveNextHandler: this.SubmitOfferHandler };

    return (
      <div>
        <BlockUi
          tag='div'
          style={{ minHeight: '500px' }}
          blocking={this.state.Loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <div className='container'>
            {
              this.state.OfferData !== undefined &&
                <form action='#' method='POST' data-name='buyer-form' id='buyer-form' encType='multipart/form-data'>
                  <BuyerProfileNavigator
                    CurrentTab={this.state.SelectedTab}
                    SaveNextHandler={this.SaveNextHandler}
                    onStepNavClick={this.handleStepNavClick}
                  />
                  <OfferWelcome {...offerPart1Props} />
                  <OfferBuyerInfo {...offerPart1Props} />
                  <OfferDetails {...offerPart1Props} />
                  <OfferVerifications {...offerVerificationsProps} />
                  <OfferSubmission {...offerPart1Props} />
                  <ProfileStep5 {...stepProps} />
                  <ProfileStep6 {...stepProps} />
                  <ProfileStep7 {...stepProps} />
                  <ProfileStep8 {...stepProps} />
                  <ProfileStep9 {...stepProps} />
                </form>
            }

            {
              this.state.NotFound === true &&
                <>
                  <BuyerProfileNavigator CurrentTab={undefined} />
                  <OfferNotFound />
                </>
            }
          </div>
        </BlockUi>
      </div>
    );
  }
}

export default withCustomAuthenticator(BuyerProfileDashboard);
