import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SellerGeneralInfo';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class GeneralInfo extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.GeneralInfoFields
    };

    const occupancyDetailsFieldName = 'general_info_occupancy_type';
    const tenantFieldName = 'general_info_tenancy_rental_rate';

    var displayTenantInfo = false;

    const fieldDef = FieldProps.FieldDefs.find(field => field.FieldName === tenantFieldName);
    if (fieldDef !== undefined) {
      var dependsOnFieldName = fieldDef.DependsOn;
      var dependsOnValue = fieldDef.DependsOnValue;

      const selectedItem = InputValuesHelper.GetInputValue(InputValues, dependsOnFieldName);
      if (selectedItem !== undefined && selectedItem === dependsOnValue) {
        displayTenantInfo = true;
      }
    }

    return (
      <div className='main_form_info steppers'>
        <FormSectionTitle Title='Occupancy Details' />
        <div className='form_inputs'>
          <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: occupancyDetailsFieldName })}</div>
          <div className='inputer_form halfs'>
            {InputHelper.GenerateInput({ ...FieldProps, FieldName: occupancyDetailsFieldName, Title: undefined })}
          </div>
        </div>

        {displayTenantInfo &&
          <>
            <FormSectionTitle Title='Tenancy Information' />
            <div className='form_inputs'>
              <div className='form_inputs thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenancy_rental_rate' })}</div>
              <div className='form_inputs thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenancy_lease_expiration_date' })}</div>
              <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'general_info_tenancy_lease_docs' })}</div>
              {InputHelper.GenerateInput({
                ...FieldProps,
                FieldName: 'general_info_tenancy_lease_docs',
                Message: 'Lease Documents'
              })}

              <div className='form_title2'>Tenant Information</div><br />
              <div className='t_texts'>Name</div>
              <div className='inputer_form thirds'>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenant_first_name', TitleUnderInput: true })}
              </div>
              <div className='inputer_form thirds'>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenant_last_name', TitleUnderInput: true })}
              </div>
              <div />
              <div className='inputer_form halfs'>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenant_phone', TitleUnderInput: true })}
              </div>
              <div />
              <div className='inputer_form halfs'>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenant_email', TitleUnderInput: true })}
              </div>
            </div>

            <div className='form_inputs radios'>
              <div className='required required-options'>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'general_info_tenant_notified' })}
              </div>

            </div>

          </>}
      </div>
    );
  }
}
