import React from 'react';
import * as Consts from '../../shared/Consts';
import { ContactUsForm } from '../ContactUs/ContactUsForm';

export default class OfferSubmission extends React.Component {
  render () {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.BuyerOfferSubmission) {
      return null;
    }

    return (
      <div className='main_form_info steppers'>
        <div className='title2' style={{ marginBottom: '180px' }}>
           Thank you for the information! You're now on the road to your new home.
          <div className='title_divider' />
          <div className='step_texts' style={{ maxWidth: '80%', textAlign: 'left', color: '#000' }}><br /> Our NILE advisor will review and verify the documentation submitted along with the offer details. Once everything has been verified,
          you will receive the final offer package for your approval and signature.
          Then comes the most exciting part of all - we send the offer to the seller!<br /><br />

          In the meantime, if you would like to know what comes next in the buying process, including inspection,
          please check out our <a className='blue_text' rel='noopener noreferrer' target='_blank' href='https://www.nilehome.com/how-it-works/'>NILE FAQ</a><br /><br />
          Don't hesitate to <ContactUsForm handleSubmit={this.props.SubmitContactUsHandler}><span className='blue_text'>reach out</span></ContactUsForm> if you have any questions.
          </div>
        </div>
      </div>
    );
  }
}
