import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class Trustee extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='TRUST DETERMINATION' />
        <div className='form_inputs'>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTR_962_Trustee_Successor' })}</div>

          {
            InputValuesHelper.GetInputValue(InputValues, 'Finance_FTR_962_Trustee_Successor') === 'Original' &&
              <>
                <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Original Trustee' />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTG_091_Social_Security_Number', Label: undefined })}</div>
                <br />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTG_227_Drivers_License_Number', Label: undefined })}</div>
                <br />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTG_941_Date_Of_Birth', Label: undefined })}</div>

                <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTG_679_Lived_In_California_Last_5_Years', ExpFieldName: 'Finance_FTG_679_T_Address' }} />
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTG_208_Marital_Status' })}</div>
                <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTG_784_Has_Declared_Bankrupt', ExpFieldName: 'Finance_FTG_784_T_Status' }} />
                <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTG_178_Has_Unsatisfied_Judgements', ExpFieldName: 'Finance_FTG_178_T_Text_Explanation' }} />
                <br />

                {InputHelper.GenerateInput(
                  {
                    ...FieldProps,
                    FieldName: 'Finance_FTG_433-893_Certified',
                    Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTG_433-893_Certified' })
                  })}

                <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTG_761-n' })}</div>
                <div className='t_texts'>Upload Trust Documents</div>
                <div className='form_inputs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTG_761-u_Upload_Trust_Documents' })}</div>
              </>
          }

          {InputValuesHelper.GetInputValue(InputValues, 'Finance_FTR_962_Trustee_Successor') === 'Successor' &&
            <>
              <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Successor Trustee' />
              <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTS_958_Is_Original_Trustee_Deceased' }} />

              <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTS_010-n' })}</div>
              <div className='t_texts'>Upload Trust Documents</div>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTS_010-u_Upload_Trust_Documents' })}
            </>}
        </div>
      </>
    );
  }
}
