import 'react-app-polyfill/ie9';
import React from 'react';
import ReactDOM from 'react-dom';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import * as Consts from './shared/Consts';
import Dashboard from './containers/Dashboard';
import BuyerProfileDashboard from './containers/BuyerProfile/BuyerProfileDashboard';
import PrimaryQuestionnaireDashboard from './containers/SellerProperty/PrimaryQuestionnaireDashboard';
import GeneralInfoDashboard from './containers/SellerProperty/GeneralInfoDashboard';
import MarketingDashboard from './containers/SellerProperty/MarketingDashboard';
import PaymentDashboard from './containers/SellerProperty/PaymentDashboard';
import FinanceTaxTitleDashboard from './containers/SellerProperty/FinanceTaxTitleDashboard';
import SystemsFeaturesDashboard from './containers/SellerProperty/SystemsFeaturesDashboard';
import Listing from './containers/SellerProperty/Listing';
import ShowingDashboard from './containers/SellerProperty/ShowingDashboard';
import DataTreeCheck from './containers/DataTreeCheck';
import * as serviceWorker from './serviceWorker';
import { Provider } from 'react-redux';
import ReduxToastr from 'react-redux-toastr';
import store from './reducer';

// import NotFound from './containers/Marketing/NotFound';

import './css/nilehome.css';
import 'react-redux-toastr/lib/css/react-redux-toastr.min.css';
import 'react-block-ui/style.css';
import 'loaders.css/loaders.min.css';

ReactDOM.render(
  <Provider store={store}>
    <div>
      <ReduxToastr
        timeOut={4000}
        newestOnTop={false}
        preventDuplicates
        position='top-right'
        getState={(state) => state.toastr} // This is the default
        transitionIn='fadeIn'
        transitionOut='fadeOut'
        progressBar={false}
        closeOnToastrClick
      />
    </div>
    <Router basename={Consts.APP_BASE_NAME}>
      <div>
        <Route exact path='/' component={Dashboard} />
        <Route exact path='/offer' component={BuyerProfileDashboard} />

        <Route exact path='/property/primaryquestionnaire/:propertyId' component={PrimaryQuestionnaireDashboard} />
        <Route exact path='/property/generalinfo/:propertyId' component={GeneralInfoDashboard} />
        <Route exact path='/property/marketing/:propertyId' component={MarketingDashboard} />
        <Route exact path='/property/payment/:propertyId' component={PaymentDashboard} />
        <Route exact path='/property/financetaxtitle/:propertyId' component={FinanceTaxTitleDashboard} />
        <Route exact path='/property/systemsfeatures/:propertyId' component={SystemsFeaturesDashboard} />
        <Route exact path='/property/listing/:propertyId/:step' component={Listing} />
        <Route exact path='/property/listing/:propertyId' component={Listing} />
        <Route exact path='/property/listing' component={Listing} />
        <Route exact path='/property/showing/:propertyId' component={ShowingDashboard} />

        <Route exact path='/datatree' component={DataTreeCheck} />

        {/* <Route path='/' component={NotFound} /> */}
      </div>
    </Router>
  </Provider>
  , document.getElementById('root'));
serviceWorker.unregister();
