import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SellerMarketing';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class Marketing extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.MarketingFields
    };

    return (
      <div className='main_form_info steppers'>
        <FormSectionTitle Title='Excluded and Included Items' />
        <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'marketing_mat_149' }} />
        <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'marketing_mat_617' }} />

        <div className='b_texts'>
          <div className='alert'>A Note About Included And Excluded Items:</div>
          <div className='description'>Unless otherwise specified in the real estate purchase agreement to be entered into by you and the buyer, all fixtures and fittings that are attached to the Property are included, and personal property items are excluded, from the purchase price.</div>
          <div className='description'>Please contact your Nile Advisor with additional questions.</div>
        </div>

        <FormSectionTitle Title='Photography' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_map_718_photograph' })}</div>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_map_256_photograph_date' })}</div>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_map_745_photograph_date2' })}</div>
        </div>

        <FormSectionTitle Title='Market Date' />
        <div className='form_inputs'>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_mad_973_market_date' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_mad_236_escrow_period' })}</div><br />
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_mad_099_vacancy' })}</div><br />
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'marketing_mad_099_in_prosession_days' })}</div>
        </div>
      </div>
    );
  }
}
