import React from 'react';
import moment from 'moment';
export default class Footer extends React.Component {
  render () {
    return (
      <>
        <footer className='footer'>
          <div className='foot_menu'>
            <a className='foot_btn animated_fastest' href='/'>Privacy Policy</a>
            <a className='foot_btn animated_fastest' href='/'>Terms of Use</a>
            <div className='foot_text'>© {moment().format('YYYY')} Nile</div>
          </div>
        </footer>
      </>
    );
  }
}
