import React from 'react';
import imageMArrow from '../images/m_arrow.svg';
import imageLogo from '../images/logo.svg';
import imageLogoW from '../images/logo_w.svg';
import { Auth } from 'aws-amplify';
import * as Consts from '../shared/Consts';

export default class Header extends React.Component {
  constructor (props) {
    super();
    this.handleSignOut = this.handleSignOut.bind(this);
  }

  async handleSignOut () {
    await Auth.signOut();
    this.props.history.push('/');
  }

  render () {
    var email;
    if (Auth.user !== null && Auth.user.attributes !== null) {
      email = Auth.user.attributes.email;
    }

    return (
      <>
        <div className='mobile_header mobile'>
          <div id='hamburger'>
            <div id='nav-icon'>
              <span />
              <span />
              <span />
            </div>
          </div>
          <a href='/' className='m_logo'>
            <img src={imageLogoW} width='100%' alt='LogoW' />
          </a>
        </div>

        <div className='mobile_menu mobile off_to_side animated_faster'>
          <div className='m_inner_menu'>
            <div className='m_column'>
              <div className='m_btn animated_fastest m_btn_active'>
                <a href='/'>
                  <div className='m_arrow animated_fastest'>
                    <img src={imageMArrow} width='100%' alt='M Arrow' />
                  </div>
                Home
                </a>
              </div>
              <div className='m_btn animated_fastest'>
                <a href='/about'>
                  <div className='m_arrow animated_fastest'>
                    <img src={imageMArrow} width='100%' alt='M Arrow' />
                  </div>
                      About
                </a>
              </div>
            </div>
            <div className='m_column'>
              <div className='m_btn animated_fastest'>
                <a href='/soldhomes'>
                  <div className='m_arrow animated_fastest'>
                    <img src={imageMArrow} width='100%' alt='M Arrow' />
                  </div>
                      Sold Homes
                </a>
              </div>
              <div className='m_btn animated_fastest'>
                <a href='/contact'>
                  <div className='m_arrow animated_fastest'>
                    <img src={imageMArrow} width='100%' alt='M Arrow' />
                  </div>
                      Contact Us
                </a>
              </div>
              <div className='m_btn animated_fastest'>
                <a href='/media'>
                  <div className='m_arrow animated_fastest'>
                    <img src={imageMArrow} width='100%' alt='M Arrow' />
                  </div>
                      Media
                </a>
              </div>
            </div>
          </div>

        </div>
        <div className='sticky desktop'>
          <a href='/' className='logo'>
            <img src={imageLogo} width='100%' alt='Logo' />
          </a>
        </div>

        <header className='top_header desktop'>
          <div className='menu'>
            <a href='/' className='menu_btn'>Home</a>
            <a href='/about' className='menu_btn'>About</a>
            <a href='/soldhomes' className='menu_btn'>Sold Homes</a>
            <a href='/contact' className='menu_btn'>Contact Us</a>
            <a href='/media' className='menu_btn'>Media</a>
            <div className='sub_menu'>
              {
                this.props.authState === Consts.AUTH_STATUS_SIGNED_IN &&
                  <>
                    <span className='t_texts' style={{ paddingRight: '10px' }}>{email}</span>
                    <div onClick={this.handleSignOut} className='menu_btn_2'>Sign Out</div>
                  </>
              }
            </div>
          </div>
        </header>
      </>
    );
  }
}
