import React from 'react';
import FooterFirstStep from './FooterFirstStep';
import * as Consts from '../../../shared/Consts';

export default class OfferWelcome extends React.Component {
  render () {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.STEP_1) {
      return null;
    }

    return (
      <div className='steppers'>

        <div className='step_title'>Welcome to Nile.</div>
        <div className='step_texts'>We Created Nile to bring you an easeful and simple buying experience. We need to gather some information so once you find the perfect property, everything is in place to write an offer. </div>
        <FooterFirstStep
          SaveHandler={() => { this.props.SaveHandler(); }}
          SaveNextHandler={() => { this.props.GoToStepHandler(Consts.STEP_2); }}
        />
      </div>
    );
  }
}
