import React, { Component } from 'react';
import { Auth } from 'aws-amplify';
import imageArrowW from '../../images/arrow_w.svg';
import * as Consts from '../../shared/Consts';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

const ClickableSpan = (onClickHandler, text) => {
  return (
    <span id='sin' style={{ maxWidth: '500px' }} className='next_btn animated_fastest' onClick={onClickHandler}>{text}
      <div className='lm_arrow'><img src={imageArrowW} width='100%' alt='arrow w' /></div>
    </span>
  );
};

const InputItem = (title, name, type, placeholder, autoComplete, onChangeHandler, onKeyPressHandler) => {
  const onKeyPressHandlerInner = (event) => {
    if (event.which === 13) { event.preventDefault(); onKeyPressHandler(); }
  };
  const input =
    <input
      className='form2'
      key={name}
      name={name}
      onChange={onChangeHandler}
      onKeyPress={onKeyPressHandlerInner}
      type={type}
      placeholder={placeholder}
      autoComplete={autoComplete}
    />;

  return InputItemWrapper(title, input);
};

const InputItemWrapper = (title, input) => {
  return (
    <div className='form_inputs' style={{ marginBottom: '0px' }}>
      <div className='t_texts'>{title}</div>
      <div className='inputer_form fulls' style={{ height: '50px', marginBottom: '0' }}>
        {input}
      </div>
    </div>
  );
};

const AuthNavi = (alertText, linkText, onClickHandler) => {
  const child =
    <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px' }}>
      {alertText !== undefined ? <span style={{ paddingRight: '5px' }}>{alertText}</span> : null}
      <span className='pass_btn animated_regular' style={{ cursor: 'pointer' }} onClick={onClickHandler}>{linkText}</span>
    </div>;
  return FormInputWrapper(child);
};

const FormInputWrapper = (child) => {
  return (
    <div className='form_inputs' style={{ marginBottom: '0px' }}>
      {child}
    </div>);
};

const SectionTitle = (title) => {
  return (
    <div className='title2' style={{ marginBottom: '40px' }}>{title}</div>
  );
};

export class CustomAuth extends Component {
  constructor (props) {
    super(props);
    this._validAuthStates = [Consts.AUTH_STATUS_SIGN_IN, Consts.AUTH_STATUS_SIGNED_OUT, Consts.AUTH_STATUS_SIGNED_UP];

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSignInInit = this.handleSignInInit.bind(this);
    this.handleSignIn = this.handleSignIn.bind(this);
    this.handleForgotPassword = this.handleForgotPassword.bind(this);
    this.handleBackToSignIn = this.handleBackToSignIn.bind(this);

    this.handleForgotPasswordSendCode = this.handleForgotPasswordSendCode.bind(this);
    this.handleSubmitForgotPassword = this.handleSubmitForgotPassword.bind(this);
    this.handleSignUpInit = this.handleSignUpInit.bind(this);
    this.handleSignInChangePassword = this.handleSignInChangePassword.bind(this);
    this.handleSignUpSubmit = this.handleSignUpSubmit.bind(this);
    this.handleSignUpConfirm = this.handleSignUpConfirm.bind(this);

    this.state = {
      executingError: undefined,
      executing: false
    };
  }

  handleError (err) {
    if (err !== undefined && err !== null) {
      if (err.message !== undefined || err.message !== null) {
        this.setState({ executingError: `Sorry, ${err.message}` });
      } else {
        this.setState({ executingError: 'Sorry, an error has occurred.' });
      }
    } else {
      this.setState({ executingError: 'Sorry, an error has occurred.' });
    }
  }

  clearError () {
    this.setState({ executingError: undefined });
  }

  async handleSignInInit () {
    this.clearError();
    this.props.onStateChange(Consts.AUTH_STATUS_SIGN_IN, {});
  }

  async handleSignIn () {
    this.setState({ executing: true });
    const username = this.inputs.username;
    const password = this.inputs.password;
    try {
      if (username === undefined || username.trim() === '') {
        this.setState({ executingError: 'Sorry, user name / email cannot be empty.' });
      } else if (password === undefined || password === '') {
        this.setState({ executingError: 'Sorry, password cannot be empty.' });
      } else {
        const userInfo = await Auth.signIn(username, password);
        if (userInfo.challengeName !== null && userInfo.challengeName === Consts.AUTH_STATUS_NEW_PASSWORD_REQUIRED) {
          this.props.onStateChange(Consts.AUTH_STATUS_SIGN_IN_NEW_PASSWORD, {});
        } else {
          this.props.onStateChange(Consts.AUTH_STATUS_SIGNED_IN, {});
        }
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  async handleSignInChangePassword () {
    this.setState({ executing: true });
    const username = this.inputs.username;
    const oldPassword = this.inputs.password;
    const newPassword = this.inputs.change_password_password;
    try {
      if (newPassword === undefined || newPassword === '') {
        this.setState({ executingError: 'Sorry, new password cannot be empty.' });
      } else {
        const userInfo = await Auth.signIn(username, oldPassword);
        await Auth.completeNewPassword(userInfo, newPassword);
        await Auth.signIn(username, newPassword);
        this.props.onStateChange(Consts.AUTH_STATUS_SIGNED_IN, {});
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  async handleForgotPasswordSendCode () {
    this.setState({ executing: true });
    const username = this.inputs.forgot_password_username;
    try {
      if (username === undefined || username.trim() === '') {
        this.setState({ executingError: 'Sorry, user name / email cannot be empty.' });
      } else if (Consts.EMAIL_REX.test(username) === false) {
        this.setState({ executingError: 'Sorry, invalid user name / email.' });
      } else {
        await Auth.forgotPassword(username);
        this.props.onStateChange(Consts.AUTH_STATUS_FORGOT_PASSWORD_SENT_CODE, {});
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  async handleSubmitForgotPassword () {
    this.setState({ executing: true });
    const username = this.inputs.forgot_password_username;
    const code = this.inputs.forgot_password_code;
    const password = this.inputs.forgot_password_password;

    try {
      if (code === undefined || code.trim() === '') {
        this.setState({ executingError: 'Sorry, code cannot be empty.' });
      } else if (password === undefined || password === '') {
        this.setState({ executingError: 'Sorry, password cannot be empty.' });
      } else {
        await Auth.forgotPasswordSubmit(username, code, password);
        await Auth.signIn(username, password);
        this.props.onStateChange(Consts.AUTH_STATUS_SIGNED_IN, {});
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  handleInputChange (event) {
    this.inputs = this.inputs || {};
    const { name, value, type, checked } = event.target;
    const checkType = ['radio', 'checkbox'].includes(type);
    this.inputs[name] = checkType ? checked : value;
    this.inputs.checkedValue = checkType ? value : null;
    this.clearError();
  }

  async handleSignUpSubmit () {
    this.setState({ executing: true });
    const username = this.inputs.signup_username;
    const password = this.inputs.signup_password;

    try {
      if (username === undefined || username.trim() === '') {
        this.setState({ executingError: 'Sorry, user name / email cannot be empty.' });
      } else if (password === undefined || password === '') {
        this.setState({ executingError: 'Sorry, password cannot be empty.' });
      } else {
        await Auth.signUp({ username, password, attributes: { email: username } });

        this.props.onStateChange(Consts.AUTH_STATUS_SIGN_UP_SUBMITTED, {});
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  async handleSignUpConfirm () {
    this.setState({ executing: true });
    const username = this.inputs.signup_username;
    const code = this.inputs.signup_code;

    try {
      if (code === undefined || code.trim() === '') {
        this.setState({ executingError: 'Sorry, code cannot be empty.' });
      } else {
        await Auth.confirmSignUp(username, code);
        this.props.onStateChange(Consts.AUTH_STATUS_SIGN_IN, {});
      }
    } catch (err) {
      this.handleError(err);
    }
    this.setState({ executing: false });
  }

  handleSignUpInit () {
    this.clearError();
    this.props.onStateChange(Consts.AUTH_STATUS_SIGN_UP_INITIALIZED, {});
  }

  handleForgotPassword () {
    this.clearError();
    this.props.onStateChange(Consts.AUTH_STATUS_FORGOT_PASSWORD_INITIALIZED, {});
  }

  handleBackToSignIn () {
    this.clearError();
    this.props.onStateChange(Consts.AUTH_STATUS_SIGN_IN, {});
  }

  render () {
    var ErrorMessageDiv;
    if (this.state.executingError !== undefined) {
      ErrorMessageDiv = <div className='sorry animated_regular'>{this.state.executingError}</div>;
    }

    return (
      <BlockUi
        tag='div'
        blocking={this.state.executing}
        loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
      >

        {
          this._validAuthStates.includes(this.props.authState) &&
            <form onSubmit={this.handleSignIn}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Sign In')}
                  {InputItem('Email Address', 'username', 'text', 'Enter your email', 'username', this.handleInputChange, this.handleSignIn)}
                  {InputItem('Password', 'password', 'password', 'Enter your password', '', this.handleInputChange, this.handleSignIn)}
                  {AuthNavi('Forgot your password?', 'Click here.', this.handleForgotPassword)}
                  {ClickableSpan(this.handleSignIn, 'Sign In')}
                  {ErrorMessageDiv}
                </div>
                <div className='log_block'>
                  {SectionTitle('Join Our List')}
                  <div className='form_inputs'>
                    <div className='greyer' style={{ marginLeft: '0', marginBottom: '50px', lineHeight: '26px' }}>
                      <span style={{ color: '#111' }}>We're always excited to share upcoming news, events, and listings with our insider list - join now to start getting all of our NILE updates.
                      </span>
                    </div>
                  </div>

                  {ClickableSpan(this.handleSignUpInit, 'Sign Up')}
                </div>
              </div>
            </form>
        }

        {
          this.props.authState === Consts.AUTH_STATUS_SIGN_IN_NEW_PASSWORD &&
            <form onSubmit={this.handleSignInChangePassword}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Change Password')}
                  {/* {InputItem('Email Address', 'username', 'text', 'Enter your email', 'username', this.handleInputChange)} */}
                  {InputItem('New Password', 'change_password_password', 'password', 'Enter your new password', 'new-password', this.handleInputChange, this.handleSignInChangePassword)}
                  <div>Password must contain at least one capital letter, one number and one special character</div>
                  {/* {AuthNavi('Forgot your password?', 'Click here.', this.handleForgotPassword)} */}
                  {AuthNavi(undefined, 'Back to Sign In', this.handleSignInInit)}
                  {/* {ClickableSpan(this.handleSignIn, 'Back to Sign In')} */}

                  {ClickableSpan(this.handleSignInChangePassword, 'Change')}
                  {ErrorMessageDiv}
                </div>
              </div>
            </form>
        }

        {
          this.props.authState === Consts.AUTH_STATUS_SIGN_UP_INITIALIZED &&
            <form onSubmit={this.handleSignUpSubmit}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Sign Up')}
                  {InputItem('Email Address', 'signup_username', 'text', 'Enter your email', 'new-email', this.handleInputChange, this.handleSignUpSubmit)}
                  {InputItem('Password', 'signup_password', 'password', 'Enter your password', 'new-password', this.handleInputChange, this.handleSignUpSubmit)}
                  {AuthNavi('Have an account?', 'Sign In.', this.handleSignInInit)}
                  {ClickableSpan(this.handleSignUpSubmit, 'Sign Up')}
                  {ErrorMessageDiv}
                </div>
              </div>
            </form>
        }

        {
          this.props.authState === Consts.AUTH_STATUS_SIGN_UP_SUBMITTED &&
            <form onSubmit={this.handleSignUpConfirm}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Sign Up')}
                  {InputItem('Code', 'signup_code', 'text', 'Enter your code', 'new-code', this.handleInputChange, this.handleSignUpConfirm)}
                  {AuthNavi('Have an account?', 'Sign In.', this.handleSignInInit)}
                  {ClickableSpan(this.handleSignUpConfirm, 'Sign Up')}
                  {ErrorMessageDiv}
                </div>
              </div>
            </form>
        }

        {
          this.props.authState === Consts.AUTH_STATUS_FORGOT_PASSWORD_INITIALIZED &&
            <form onSubmit={this.handleForgotPasswordSendCode}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Reset Password')}
                  {InputItem('Email Address', 'forgot_password_username', 'text', 'Enter your email', 'username', this.handleInputChange, this.handleForgotPasswordSendCode)}
                  {AuthNavi(undefined, 'Back to Sign In', this.handleBackToSignIn)}
                  {ClickableSpan(this.handleForgotPasswordSendCode, 'Send Code')}
                  {ErrorMessageDiv}
                </div>
              </div>
            </form>
        }

        {
          this.props.authState === Consts.AUTH_STATUS_FORGOT_PASSWORD_SENT_CODE &&
            <form onSubmit={this.handleSubmitForgotPassword}>
              <div className='logins'>
                <div className='log_block'>
                  {SectionTitle('Reset Password')}
                  {
                    InputItemWrapper('Email Address',
                      <input
                        className='form2'
                        key='forgot_password_username2'
                        name='forgot_password_username2'
                        type='email'
                        placeholder='Enter your email'
                        value={this.inputs.forgot_password_username}
                        readOnly
                        autoComplete='username'
                      />)
                  }
                  {InputItem('New Password', 'forgot_password_password', 'password', 'Enter your password', 'new-password', this.handleInputChange, this.handleSubmitForgotPassword)}
                  {InputItem('Code', 'forgot_password_code', 'text', 'Enter your code', 'new-code', this.handleInputChange, this.handleSubmitForgotPassword)}
                  {AuthNavi(undefined, 'Back to Sign In', this.handleBackToSignIn)}
                  {ClickableSpan(this.handleSubmitForgotPassword, 'Submit')}
                  {ErrorMessageDiv}
                </div>
              </div>
            </form>
        }
      </BlockUi>
    );
  }
}
