import React from 'react';
import axios from 'axios';
import { toastr } from 'react-redux-toastr';

export default class DataTreeCheck extends React.Component {
  constructor () {
    super();

    this.handleAddressChange = this.handleAddressChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.state = {
      PropertyAddress: '',
      Response: undefined,
      StatusCode: undefined,
      StatusText: undefined
    };
  }

  componentDidMount () {

  }

  handleAddressChange (event) {
    this.setState({ PropertyAddress: event.target.value });
  }

  async handleSubmit () {
    if (this.state.PropertyAddress !== undefined && this.state.PropertyAddress.trim() !== '') {
      const dataTreeBaseUrl = 'https://dtapiuat.datatree.com:443';
      var token = '';

      try {
        const authenticateUrl = `${dataTreeBaseUrl}/api/Login/Authenticate`;
        const authenticateBody = { Username: 'DTAPI_RiverChase_UAT', Password: 'RiverChaseDTAPI@1UAT' };
        const authenticateRequestConfig = {
          headers: {
            'Content-Type': 'application/json',
            IncludeHash: true
          }
        };
        const authResponse = await axios.post(authenticateUrl, authenticateBody, authenticateRequestConfig);
        token = authResponse.data;
      } catch (error) {
        console.log(error);
        toastr.error('Verify property address', 'Please refresh page and try again');
        return;
      }

      try {
        const reportUrl = `${dataTreeBaseUrl}/api/Report/GetReport`;
        const reportBody = {
          ProductNames: [
            'PropertyDetailReport', 'DataTreeAVM'
          ],
          SearchType: 'FullAddress',
          FullAddress: this.state.PropertyAddress,
          ReferenceId: '1234'
        };
        const reportRequestConfig = {
          headers: {
            'Content-Type': 'application/json',
            Authorization: token
          }
        };

        const reportResponse = await axios.post(reportUrl, reportBody, reportRequestConfig);
        console.log(reportResponse.status);
        console.log(reportResponse.statusText);

        if (reportResponse.status === 200) {
          this.setState({ Response: reportResponse.data, StatusCode: reportResponse.status, StatusText: reportResponse.statusText });
        } else {
          this.setState({ Response: undefined, StatusCode: reportResponse.status, StatusText: reportResponse.statusText });
        }
      } catch (error) {
        console.log(error);
        toastr.warning('Verify property address', 'Please check the address and submit again!');
      }
    } else {
      this.setState({ Response: undefined, StatusCode: undefined, StatusText: undefined });
    }
  }

  render () {
    return (
      <div style={{ maxWidth: '960px', margin: 'auto', paddingTop: '50px' }}>
        <input style={{ width: '350px' }} type='text' name='address' onChange={this.handleAddressChange} />
        <button style={{ margin: '25px' }} onClick={this.handleSubmit}>Submit</button>

        <br />
        <a href='https://jsonformatter.curiousconcept.com/' target='_new'>https://jsonformatter.curiousconcept.com/</a>

        <div>
          {this.state.StatusCode} {this.state.StatusText}
        </div>

        <div>
          {this.state.Response !== undefined &&
            <textarea style={{ height: '400px', width: '800px' }} defaultValue={JSON.stringify(this.state.Response)} />}
        </div>
      </div>
    );
  }
}
