import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PlumbingElectrical extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Plumbing & Electrical' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_083_Plumbing_Features' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_547_Water_Heater_Power' })}</div><br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_932_Has_Laundry_Hookups' })}</div><br />
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_479_Electrical_Features' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFL_479_Electrical_Features', '220VoltWiring') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_147_Loations_Of_Wiring' })}</div>
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFL_147_Loations_Of_Wiring', 'Other') &&
                    <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_147_t_Text_Explanation' })}</div>
                }
              </>
          }
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_874_Defects' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFL_874_Defects', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFL_874_t_Text_Explanation' })}</div>
          }

        </div>
      </>
    );
  }
}
