import React from 'react';
import * as Utils from './Utils';

export default class SingleOption extends React.Component {
  render () {
    const {
      FieldName,
      Label,
      Title,
      AfterLabelLink,
      ValueChangeHandler,
      ValidationResults,
      InputValues
    } = this.props;

    const validationError = Utils.GetValidationResult(ValidationResults, FieldName);
    let checked = '';
    if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
      const fieldValue = InputValues[FieldName];
      checked = (fieldValue === true ? 'checked' : '');
    }

    return (
      <>
        <span className='form-multiple-choise-wrapper__item'>
          <input
            type='checkbox'
            id={'checkbox_' + FieldName} name={FieldName}
            onChange={(event) => {
              ValueChangeHandler(this, FieldName, this, event.target.checked);
            }}
            checked={checked}
            disabled={this.props.ReadOnly === true ? 'disabled' : ''}
          />
          {
            Title !== undefined &&
              <label className='t_texts' style={{ color: '#111', fontSize: '15px', paddingLeft: '18px', paddingRight: '5px', marginRight: '0px', marginBottom: '0px' }} htmlFor={'checkbox_' + FieldName}>{Title}</label>
          }
          {
            Label !== undefined &&
              <label className='t_texts' style={{ color: '#111', fontSize: '15px', paddingLeft: '18px', paddingRight: '5px', marginRight: '0px', marginBottom: '0px' }} htmlFor={'checkbox_' + FieldName}>{Label}</label>
          }
          {AfterLabelLink}
          {
            validationError !== undefined &&
              <>
                <div className='validation-failed' />
                {validationError}
              </>
          }
        </span>
      </>
    );
  }
}
