import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class TitleHolderIndividual extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='Individual Title Holder' />
        <div className='form_inputs'>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTU_383_Social_Security_Number', Label: undefined })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTU_713_Drivers_License_Number', Label: undefined })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTU_970_Date_Of_Birth', Label: undefined })}</div>
          <br />
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTU_579_Marital_Status', Title: undefined })}</div>

          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTU_579-565_Lived_In_California_Last_5_Years', ExpFieldName: 'Finance_FTU_579_T_Address' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTU_261_Has_Declared_Bankrupt', ExpFieldName: 'Finance_FTU_261_T_Text_Explanation' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTU_533_Has_Unsatisfied_Judgements', ExpFieldName: 'Finance_FTU_533_T_Text_Explanation' }} />

          {InputHelper.GenerateInput(
            {
              ...FieldProps,
              FieldName: 'Finance_FTU_991_881_Certified',
              Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTU_991_881_Certified' })
            })}

        </div>
      </>
    );
  }
}
