import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as InputHelper from '../Shared/InputHelper';
import * as InputValuesHelper from '../../shared/InputValuesHelper';
import * as Consts from '../../shared/Consts';

export default class OfferDetails extends React.Component {
  render () {
    const {
      InputValues,
      OfferData,
      CurrentTab
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.BuyerFields
    };

    if (CurrentTab !== Consts.BuyerOfferDetails) {
      return null;
    }

    // get property data
    var propertyData = {};
    if (OfferData !== undefined && OfferData.PropertyData !== undefined) {
      propertyData = OfferData.PropertyData;
    }

    const {
      Address,
      BathRooms,
      BedRooms,
      FirstPhoto,
      ListPriceFormatted
    } = propertyData;

    return (
      <>
        <div className='main_form_info steppers'>
          <div className='title2'>Make an Offer</div>
          <div className='b_texts'>Well this is exciting, it's time to make an offer!  Let's verify all the details related to the offer and the price.</div>

          <div className='pro_tab'>
            <div className='tab1'>Property</div>
            <div className='tab_divider desktop' />
            <div className='tab2 desktop'>Please confirm that this is the property you would like to put an offer on.</div>
          </div>

          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}>
            <span style={{ color: '#111' }}>Note:</span> Please confirm that this is the property you would like to put an offer on.
          </div>

          <div className='form_inputs'>
            <div className='inputer_form thirds'>
              <img src={FirstPhoto} width='100%' alt='property' />
            </div>
            <div className='inputer_form halfs'>
              <div className='t_texts'><strong>Address: </strong>{Address}</div>
              <div className='t_texts'><strong>Bed(s): </strong>{BedRooms}</div>
              <div className='t_texts'><strong>Bath(s): </strong>{BathRooms}</div>
              <div className='t_texts'><strong>Listing Price: </strong>{ListPriceFormatted}</div>
            </div>
          </div>

          <div className='pro_tab'>
            <div className='tab1'>Offer</div>
            <div className='tab_divider desktop' />
            <div className='tab2 desktop'>Place your offer and indicate your preferred escrow period. We recommend the standard escrow period: 30 days.</div>
          </div>

          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}>
            <span style={{ color: '#111' }}>Note: </span> Place an offer for the property you would like to purchase along with the days in escrow.
          </div>

          <div className='form_inputs'>
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_price' })}
            </div>
            <br />
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_escrow_days' })}
            </div>
            <br />
            <div className='inputer_form thirds'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_contingency_period' })}
            </div>

            <br />
            <div className='inputer_form halfs4'>
              <div className='b_texts'>
                {InputHelper.GetFieldDescription({ ...FieldProps, FieldName: 'buyer_init_offer_include_other_items' })}
              </div>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_include_other_items' })}
            </div>

            {InputValuesHelper.GetInputValue(InputValues, 'buyer_init_offer_include_other_items') === Consts.OPTION_YES_VALUE &&
              <>
                <br />
                <div className='inputer_form halfs'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_other_items' })}
                </div>
              </>}
          </div>

          <div className='pro_tab'>
            <div className='tab1'>Contingency</div>
          </div>
          <div className='form_inputs'>
            <div className='inputer_form halfs'>
              {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_contingency' })}
            </div>
          </div>

          {InputValuesHelper.GetInputValue(InputValues, 'buyer_init_offer_home_sale_contingency') === Consts.OPTION_YES_VALUE &&
            <>
              <div className='pro_tab'>
                <div className='tab1'>Property to be Sold</div>
              </div>
              <div className='form_inputs'>
                <div className='t_texts'>
                  {InputHelper.GetFieldDescription({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_address' })}
                </div>
                <div className='inputer_form halfs'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_address', TitleUnderInput: true })}
                </div>
                <br />
                <div className='inputer_form thirds'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_city', TitleUnderInput: true })}
                </div>
                <div className='inputer_form thirds'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_state', TitleUnderInput: true })}
                </div>
                <br />
                <div className='inputer_form thirds'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_postal_code', TitleUnderInput: true })}
                </div>
                <br />
                <div className='inputer_form thirds'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_dwelling_type' })}
                </div>
                <br />
                <div className='inputer_form halfs'>
                  {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_sale_property_status' })}
                </div>

                {/* not yet listed for sale */}
                {InputValuesHelper.GetInputValue(InputValues, 'buyer_init_offer_sale_property_status') === 'not_listed' &&
                  <>
                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_not_listed_ready_on_market' })}
                    </div>
                  </>}

                {/* active on market */}
                {InputValuesHelper.GetInputValue(InputValues, 'buyer_init_offer_sale_property_status') === 'on_market' &&
                  <>
                    <br />
                    <div className='inputer_form halfs'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_date' })}
                    </div>
                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_received_offer' })}
                    </div>
                    <br />
                    <div className='t_texts'>
                      {InputHelper.GetFieldDescription({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_name' })}
                    </div>
                    <div className='inputer_form halfs'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_name', TitleUnderInput: true })}
                    </div>

                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_first_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_last_name', TitleUnderInput: true })}
                    </div>
                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_phone', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_on_market_agent_email', TitleUnderInput: true })}
                    </div>
                  </>}
                {/* in escrow */}
                {InputValuesHelper.GetInputValue(InputValues, 'buyer_init_offer_sale_property_status') === 'in_escrow' &&
                  <>
                    <br />
                    <div className='inputer_form halfs'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_released_contingencies' })}
                    </div>
                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_in_escrow_expected_closing_date' })}
                    </div>
                    <br />
                    <div className='inputer_form halfs'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_escrow_settlement_name' })}
                    </div>
                    <br />
                    <div className='t_texts'>
                      {InputHelper.GetFieldDescription({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_name' })}
                    </div>
                    <div className='inputer_form halfs'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_name', TitleUnderInput: true })}
                    </div>

                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_first_name', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_last_name', TitleUnderInput: true })}
                    </div>
                    <br />
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_phone', TitleUnderInput: true })}
                    </div>
                    <div className='inputer_form thirds'>
                      {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'buyer_init_offer_home_sale_in_escrow_agent_email', TitleUnderInput: true })}
                    </div>
                  </>}
              </div>
            </>}
        </div>
        <FooterMiddleStep
          SaveHandler={() => { this.props.SaveHandler(DataSections); }}
          HideSaveButton={this.props.ReadOnly === true}
          PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.BuyerOfferBuyerInfo); }}
          SaveNextHandler={() => { this.props.SaveNextHandler(Consts.BuyerOfferVerifications, DataSections); }}
        />
      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_BUYER_INIT_OFFER
];
