import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class WireInstructions extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='Outgoing Wire Instructions' />
        <div className='form_inputs'>
          <div className='inputer_form halfs'>
            {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_441_Domestic_International' })}
          </div>
          <br />
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_377_Bank_Name', Label: undefined })}</div>
          <br />
          <div className='form_title2'>Bank Address</div>
          <br />
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_523_Street_Address', TitleUnderInput: true })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_299_City', TitleUnderInput: true })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_174_State', TitleUnderInput: true })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_650_Postal_Code', TitleUnderInput: true })}</div>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_175_Country', TitleUnderInput: true })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_247_Routing_Number' })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_655_Swift_Code' })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_440_Account_Name' })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_985_Account_Number' })}</div>
          <br />
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_146_For_Further_Credit_To' })}</div>
          <br />
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTW_708_Note' })}</div>
        </div>
      </>
    );
  }
}
