import 'react-app-polyfill/ie9';
import React from 'react';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { Auth } from 'aws-amplify';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import leftArrow from '../../images/l_arrow.svg';
import './Showing.css';
import moment from 'moment';
import uuidv4 from 'uuid';

class ShowingDashboard extends React.Component {
  constructor (props) {
    super();

    this.handleDateTimeClick = this.handleDateTimeClick.bind(this);

    console.log(Auth);

    this.state = {
      propertyId: props.match.params.propertyId,
      SelectedDateTime: []
    };
  }

  async componentDidMount () {

  }

  handleDateTimeClick (dateTime) {
    console.log(dateTime);
    var selectedDateTime = this.state.SelectedDateTime;
    if (selectedDateTime.find(dt => dt.Date === dateTime.Date && dt.Time === dateTime.Time) !== undefined) {
      selectedDateTime = selectedDateTime.filter(dt => (dt.Date !== dateTime.Date || (dt.Date === dateTime.Date && dt.Time !== dateTime.Time)));
    } else {
      selectedDateTime.push({ Date: dateTime.Date, Time: dateTime.Time });
    }
    console.log(selectedDateTime);
    this.setState({ SelectedDateTime: selectedDateTime });
  }

  render () {
    const startDate = moment().startOf('day').add(1, 'day');
    const endDate = moment().startOf('day').add(6, 'day');

    var weekdays = [];
    var startTimes = [];
    for (var d = 1; d <= 7; d++) {
      weekdays.push(moment().startOf('day').add(d, 'day'));
      for (var m = 540; m < 1080; m += 30) {
        startTimes.push({
          Date: moment().startOf('day').add(d, 'day').format('YYYY/MM/DD'),
          Time: m,
          DateTime: moment().startOf('day').add(d, 'day').add(m, 'm')
        });
      }
    }

    var startTimeScales = [];
    for (var minutes = 540; minutes < 1080; minutes += 30) {
      startTimeScales.push({
        Time: minutes,
        DateTime: moment().startOf('day').add(minutes, 'm')
      });
    }

    return (
      <div>
        <BlockUi
          tag='div'
          blocking={this.state.Loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <div className='container'>
            <form action='#' method='POST' data-name='buyer-form' id='buyer-form' encType='multipart/form-data'>
              <div className='calendar'>

                <div className='cal_title'>Please select your availability to show</div>
                <div className='cal_line' />

                <div className='cal'>
                  <div className='cal_arrows'>
                    <div className='cal_arrow lefts_cal animated_fastest'><img src={leftArrow} width='100%' alt='Left Arrow' /></div>
                    <div className='cycle-slideshow c_dates' data-cycle-fx='fade' data-cycle-timeout='0' data-cycle-prev='.lefts_cal' data-cycle-next='.rights_cal' data-cycle-easing='easeInOutQuad' data-cycle-speed='500' data-cycle-swipe='true' data-cycle-slides='> .slide' style={{ position: 'relative' }}>
                      <div className='slide cycle-slide cycle-sentinel cal__sentinel'>{startDate.format('MMM')} {startDate.format('Do')}-{endDate.format('Do')}</div>
                      <div className='slide cycle-slide cycle-slide-active cal__title'>{startDate.format('MMM')} {startDate.format('Do')}-{endDate.format('Do')}</div>
                      {/* <div className='slide cycle-slide cal__slide'>Jan 19th-24th</div> */}
                    </div>
                    <div className='cal_arrow rights_cal animated_fastest'><img src={leftArrow} width='100%' alt='Left Arrow' /></div>
                  </div>
                  <div className='cal_slideshow'>
                    <div className='cycle-slideshow cal_times desktop' data-cycle-fx='fade' data-cycle-timeout='0' data-cycle-prev='.lefts_cal' data-cycle-next='.rights_cal' data-cycle-easing='easeInOutQuad' data-cycle-speed='500' data-cycle-swipe='true' data-cycle-slides='> .slide'>
                      {/* <div className='slide cycle-slide cycle-sentinel cal__sentinel'>
                        <div className='c_time' style={{ visibility: 'hidden' }}>9 AM - 12 PM</div>
                        <div className='c_time' style={{ visibility: 'hidden' }}>1 PM - 5 PM</div>
                      </div> */}
                      <div className='slide cycle-slide cycle-slide-active cal__title'>
                        {
                          startTimeScales.map(startTimeScale =>
                            <div key={uuidv4()} className='c_time_narrow'>{startTimeScale.DateTime.format('h:mm A')}</div>
                          )
                        }
                      </div>
                      {/* <div className='slide cycle-slide cal__slide'>
                        <div className='c_time'>10 AM - 1 PM</div>
                        <div className='c_time'>3 PM - 5:30 PM</div>
                        <div className='c_time'>6 PM - 6:30 PM</div>
                      </div> */}
                    </div>

                    <div
                      className='cycle-slideshow charts' data-cycle-fx='scrollHorz' data-cycle-timeout='0' data-cycle-prev='.lefts_cal' data-cycle-next='.rights_cal' data-cycle-auto-height='container' data-cycle-easing='easeInOutQuad' data-cycle-speed='500' data-cycle-swipe='true' data-cycle-slides='> .slide'
                      style={{ position: 'relative', height: '680px' }}
                    >
                      <div className='slide cycle-slide cycle-slide-active cal__title'>
                        <div className='cal_headers1'>
                          {
                            weekdays.map(weekday =>
                              <div key={uuidv4()} className='cal_seven_dates'>{weekday.format('ddd')}</div>
                            )
                          }
                        </div>

                        {
                          startTimeScales.map(startTimeScale => {
                            return (
                              <div key={uuidv4()} className='cal_headers_narrow'>
                                {
                                  startTimes.filter(startTime => startTime.Time === startTimeScale.Time).map(startTime => {
                                    return (
                                      <div key={uuidv4()} className='cal_seven_blocks'>
                                        <div className='cal_fills' onClick={this.handleDateTimeClick.bind(this, startTime)}>
                                          {
                                            this.state.SelectedDateTime.find(dt => dt.Date === startTime.Date && dt.Time === startTime.Time) !== undefined &&
                                              <div className='c_green c_actives' />
                                          }

                                          {/* <div className='c_green' /> */}
                                          {/* N/A */}
                                        </div>
                                      </div>
                                    );
                                  })
                                }
                              </div>
                            );
                          }
                          )
                        }
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </BlockUi>
      </div>
    );
  }
}

export default withCustomAuthenticator(ShowingDashboard);
