import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Loan extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='Loan' />
        <div className='form_inputs'>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_463_Has_Loan' })}</div>

          {
            InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_463_Has_Loan') === Consts.OPTION_YES_VALUE &&
              <>
                <div className='form_title2'>Loan 1</div>
                <br />
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-357_Institution_Name' })}</div>
                <br />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-996_Account_Number' })}</div>

                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-731_Estimated_Payoff' })}</div>
                <br />
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-383_Street_Address' })}</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-713_City' })}</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-970_State' })}</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-579_Postal_Code' })}</div>
                <br />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-919_Phone' })}</div>
                <br />
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-357_Fax' })}</div>

                <div className='t_texts'>Upload Statement</div>
                {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_802-u_Statement' })}

                <div className='t_texts'>Please Provide the Following information</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_630-501_Borrower_Ssn' })}</div>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_630-564_Borrower_Dob' })}</div>
                <br />
                <div className='inputer_form twothirds'>{InputHelper.GenerateInput(
                  {
                    ...FieldProps,
                    FieldName: 'Finance_FTL_404_Authorized_Nile',
                    Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTL_404_Authorized_Nile' })
                  })}
                </div>

                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_887_Has_Second_Loan' })}</div>

                {
                  InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_887_Has_Second_Loan') === Consts.OPTION_YES_VALUE &&
                    <>
                      <div className='form_title2'>Loan 2</div>
                      <br />
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-565_Name' })}</div>
                      <br />
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-261_Account_Number' })}</div>
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-533_Estimated_Payoff' })}</div>
                      <br />
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-881_Street_Address' })}</div>
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-831_City' })}</div>
                      <br />
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-091_State' })}</div>
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-227_Postal_Code' })}</div>
                      <br />
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-941_Phone' })}</div>
                      <br />
                      <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_956-679_Fax' })}</div>
                      <br />
                      {
                        InputHelper.GenerateInput(
                          {
                            ...FieldProps,
                            FieldName: 'Finance_FTL_545_Authorized_Nile',
                            Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTL_545_Authorized_Nile' })
                          })
                      }
                      <br />

                      <div className='inputer_form thirds'>
                        {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_208_Has_Third_Loan' })}
                      </div>

                      {InputValuesHelper.GetInputValue(InputValues, 'Finance_FTL_208_Has_Third_Loan') === Consts.OPTION_YES_VALUE &&
                        <>
                          <div className='form_title2'>Loan 3</div>
                          <br />
                          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-844_Name' })}</div>
                          <br />
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-310_Account_Number' })}</div>
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-946_Estimated_Payoff' })}</div>
                          <br />
                          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-784_Street_Address' })}</div>
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-178_City' })}</div>
                          <br />
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-878_State' })}</div>
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-761_Postal_Code' })}</div>
                          <br />
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-433_Phone' })}</div>
                          <br />
                          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTL_996-893_Fax' })}</div>
                        </>}
                    </>
                }
              </>
          }
        </div>
      </>
    );
  }
}
