import React from 'react';
import FooterLastStep from './FooterLastStep';
import * as Consts from '../../shared/Consts';

export default class ProfileStep9 extends React.Component {
  render () {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.STEP_9) {
      return null;
    }

    return (
      <div className='steppers'>
        <div className='step_title'>Placeholder</div>
        <div className='step_texts'>Placeholder</div>
        <FooterLastStep
          SaveHandler={() => { this.props.SaveHandler(); }}
          PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.STEP_8); }}
        />
      </div>
    );
  }
}
