import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class LeasedLienedItems extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Leased or Liened Items' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFD_998_Leased_Liened_Items' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFD_998_Leased_Liened_Items', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFD_762_Leased_Liened_Items' })}</div>
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFD_762_Leased_Liened_Items', 'Other') &&
                    <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFD_762_t_Text_Explanation' })}</div>
                }
              </>
          }
        </div>
      </>
    );
  }
}
