import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class KitchenFeatures extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Kitchen Features' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFK_x01_Kitchen_Features' })}</div><br />

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFK_460_Defects' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFK_460_Defects', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFK_460_t_Text_Explanation' })}</div>
          }
        </div>
      </>
    );
  }
}
