export const GetInputValue = (inputValues, fieldName) => {
  return inputValues[fieldName];
};

export const HasInputValue = (inputValues, fieldName, value) => {
  const fieldValue = inputValues[fieldName];

  if (fieldValue === undefined) {
    return false;
  } else {
    if (Array.isArray(fieldValue) && fieldValue.find(item => item.value === value)) {
      return true;
    } else if (Array.isArray(fieldValue) === false && fieldValue === value) {
      return true;
    } else {
      return false;
    }
  }
};
