import 'react-app-polyfill/ie9';
import React from 'react';
import withCustomAuthenticator from './Authenticator/withCustomAuthenticator';
import { Auth } from 'aws-amplify';
import axios from 'axios';
import * as Consts from '../shared/Consts';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import uuid from 'uuid';
import numeral from 'numeral';
import moment from 'moment';

const TAB_NAME_OFFER = 'offer';
const TAB_NAME_PROPERTY = 'property';
const STYLE_FULL_WIDTH = { width: '100%' };

const GenerateOfferList = (offers) => {
  if (offers === undefined || offers.length === 0) {
    return <p className='offer_not_exist'>Offer Not Found</p>;
  } else {
    const orderedOffers = offers.sort((itemA, itemB) => { return (moment(itemA.CreatedAt) - moment(itemB.CreatedAt)); });
    return (
      <>
        {
          orderedOffers.map((offer, index) =>
            <div key={uuid()} className='offer_block'>
              <div className='offer_container offer_title'>
                <div className='offer_item offer_no'>Offer {index + 1}</div>
                <div className='offer_item'>
                  {GetOfferAction(offer)}
                </div>
              </div>
              <div className='offer_container'>
                <div className='offer_item'>
                Property Address<br />{offer.PropertyData.Address}
                </div>
                <div className='offer_item'>
                Status<br />{GetOfferStatus(offer)}
                </div>
                <div className='offer_item'>
                Listing Price<br />{offer.PropertyData.ListPriceFormatted}
                </div>
                <div className='offer_item'>
                Your Offer Price<br />{GetOfferPrice(offer)}
                </div>
              </div>
            </div>
          )
        }
      </>
    );
  }
};

const GetOfferPrice = (offer) => {
  if (offer !== undefined) {
    if (offer.InitOffer !== undefined && offer.InitOffer.OfferPrice !== null) {
      return numeral(offer.InitOffer.OfferPrice).format('$0,0');
    } else if (offer.OfferDetail !== undefined && offer.OfferDetail.OfferPrice !== null) {
      return numeral(offer.OfferDetail.OfferPrice).format('$0,0');
    } else {
      return null;
    }
  }
};

const GetOfferStatus = (offer) => {
  if (offer.Submitted !== undefined && offer.Submitted === true) {
    return 'Submitted';
  } else {
    return 'In Progress';
  }
};

const GetOfferAction = (offer) => {
  var text = 'Continue Offer';
  if (offer.Submitted !== undefined && offer.Submitted === true) {
    text = 'See Details';
  }
  return <div className='offer_action_btn'><a href={`/${Consts.APP_BASE_NAME}/offer?offerid=${offer.OfferId}`}>{text}</a></div>;
};

const GeneratePropertyList = (properties) => {
  if (properties === undefined || properties.length === 0) {
    return <p className='offer_not_exist'>Property Not Found</p>;
  } else {
    const orderedProperties = properties.sort((itemA, itemB) => { return (moment(itemA.CreatedAt) - moment(itemB.CreatedAt)); });
    return (
      <>
        {
          orderedProperties.map((property, index) =>
            <div key={uuid()} className='offer_block'>
              <div className='offer_container offer_title'>
                <div className='offer_item offer_no'>Property {index + 1}</div>
                <div className='offer_item'>
                  {/* {GetPropertyAction(offer)} */}
                </div>
              </div>
              <div className='offer_container'>
                <div className='offer_item'>
                Property Address<br />{ConvertPropertyAddress(property.PropertyData)}
                </div>
              </div>
            </div>
          )
        }
      </>
    );
  }
};

const ConvertPropertyAddress = (propertyData) => {
  var zip = '';
  if (propertyData.Zip9 !== null && propertyData.Zip9 !== undefined) {
    const zipSections = propertyData.Zip9.split('-');
    if (zipSections.length > 0) {
      zip = zipSections[0];
    }
  }
  return `${propertyData.StreetAddress}, ${propertyData.City}, ${propertyData.State} ${zip}`;
};

class Dashboard extends React.Component {
  constructor () {
    super();
    this.switchTabHandler = this.switchTabHandler.bind(this);

    this.state = {
      SelectedTab: TAB_NAME_OFFER,
      Properties: undefined,
      Offers: undefined,
      Loading: false
    };
  }

  async componentDidMount () {
    try {
      this.setState({ Loading: true });

      const requestConfig = {
        headers: {
          'Content-Type': Consts.HEADER_APPLICATION_JSON,
          Authorization: (await Auth.currentSession()).getIdToken().getJwtToken()
        }
      };

      const getOffersUrl = `${Consts.API_BUYER_OFFER}?include=PropertyData,InitOffer,OfferDetail`;
      const getPropertiesUrl = Consts.API_SELLER_PROPERTIES;
      const responses = await axios.all([
        axios.get(getPropertiesUrl, requestConfig),
        axios.get(getOffersUrl, requestConfig)
      ]);

      this.setState({
        Loading: false,
        Properties: responses[0].data.Properties,
        Offers: responses[1].data.Offers
      });
    } catch (error) {
      this.setState({ Loading: false });
      toastr.error('Load properties', 'Please refresh page and try again');
    }
  }

  switchTabHandler (tabName) {
    this.setState({ SelectedTab: tabName });
  }

  render () {
    const selectedTab = this.state.SelectedTab;

    return (
      <BlockUi
        tag='div'
        blocking={this.state.Loading}
        loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
      >
        <div className='hiw_section hiw_section_front'>
          <div className='hiw_blocks hiw_blocks_front'>
            <div
              className={selectedTab === TAB_NAME_OFFER ? 'hiw_block hiw_block_front hiw_active' : 'hiw_block hiw_block_front'}
              onClick={this.switchTabHandler.bind(this, TAB_NAME_OFFER)}
            >
              <div className='backss' />
              <div className='hiw_inner_block animated_fastest'>Buying a Home</div>
            </div>
            <div className={selectedTab === TAB_NAME_OFFER ? 'hiw_panel2 mobile2' : 'hiw_panel2 mobile2 hidden_panel'}>
              <div className={selectedTab === TAB_NAME_OFFER ? 'hiw animated_fastest' : 'hiw animated_fastest hide_it'}>
                <div className='hiw_holder' style={{ ...STYLE_FULL_WIDTH, paddingBottom: '0' }}>
                  {GenerateOfferList(this.state.Offers)}
                </div>
              </div>
            </div>
            <div
              className={selectedTab === TAB_NAME_PROPERTY ? 'hiw_block hiw_block_front hiw_active' : 'hiw_block hiw_block_front'}
              onClick={this.switchTabHandler.bind(this, TAB_NAME_PROPERTY)}
            >
              <div className='backss' />
              <div className='hiw_inner_block animated_fastest'>Selling a Home</div>
            </div>
            <div className={selectedTab === TAB_NAME_PROPERTY ? 'hiw_panel2 mobile2' : 'hiw_panel2 mobile2 hidden_panel'}>
              <div className={selectedTab === TAB_NAME_PROPERTY ? 'hiw animated_fastest' : 'hiw animated_fastest hide_it'}>
                <div className='hiw_holder' style={{ ...STYLE_FULL_WIDTH, paddingBottom: '0' }}>
                  {GeneratePropertyList(this.state.Properties)}
                </div>
              </div>
            </div>
          </div>
          <div className='hiw_panel desktop2'>
            <div className={selectedTab === TAB_NAME_OFFER ? 'hiw' : 'hiw hide_it'}>
              <div className='hiw_holder' style={STYLE_FULL_WIDTH}>
                {GenerateOfferList(this.state.Offers)}
              </div>
            </div>
            <div className={selectedTab === TAB_NAME_PROPERTY ? 'hiw' : 'hiw hide_it'}>
              <div className='hiw_holder' style={STYLE_FULL_WIDTH}>
                {GeneratePropertyList(this.state.Properties)}
              </div>
            </div>
          </div>
        </div>
      </BlockUi>
    );
  }
}

export default withCustomAuthenticator(Dashboard);
