import React from 'react';
import uuidv4 from 'uuid/v4';
import * as Utils from './Utils';

export default function RadioGroup (props) {
  const {
    FieldName,
    FieldDefs,
    Options,
    ValueChangeHandler,
    ValidationResults,
    InputValues
  } = props;

  const validationError = Utils.GetValidationResult(ValidationResults, FieldName);
  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  const field = FieldDefs.find(field => field.FieldName === FieldName);

  return (
    <>
      {
        (field !== undefined && field.Title !== undefined) &&
          <div className='t_texts'>{field.Title}</div>
      }
      {
        Options.map((item, index) => {
          return (
            <span key={uuidv4()}>
              <input
                type='radio'
                id={`${FieldName}_${index}`}
                name={FieldName}
                value={item.value}
                onChange={() => {
                  ValueChangeHandler(this, FieldName, field.Type, item.value);
                }}
                checked={value === item.value ? 'checked' : ''}
                disabled={props.ReadOnly === true ? 'disabled' : ''}
              />
              <label
                htmlFor={`${FieldName}_${index}`}
              >{item.label}
              </label>
            </span>
          );
        })
      }
      {
        validationError !== undefined &&
          <>
            <div className='validation-failed' />
            {validationError}
          </>
      }
    </>
  );
}
