import 'react-app-polyfill/ie9';
import React from 'react';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import { Auth } from 'aws-amplify';
import ProfileStep1 from '../../components/SellerProperty/Listing/ProfileStep1';
import ProfileStep2 from '../../components/SellerProperty/Listing/ProfileStep2';
import ProfileStep3 from '../../components/SellerProperty/Listing/ProfileStep3';
import ProfileStep4 from '../../components/SellerProperty/Listing/ProfileStep4';
import ProfileStep4Confirm from '../../components/SellerProperty/Listing/ProfileStep4Confirm';
import Navigator from '../../components/SellerProperty/Listing/Navigator';
import axios from 'axios';
import * as Consts from '../../shared/Consts';
import * as Validator from '../../shared/Validator';
import * as FieldDef from '../../shared/FieldDefinition/Listing';
import * as PostDataGenerator from '../../shared/PostDataGenerator';
import * as ResponseDataConvert from '../../shared/ResponseDataConvert';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import './Listing.css';

class Listing extends React.Component {
  constructor (props) {
    super();
    this.SaveHandler = this.SaveHandler.bind(this);
    this.SaveNextHandler = this.SaveNextHandler.bind(this);
    this.handleStepNavClick = this.handleStepNavClick.bind(this);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.handleGoToStep = this.handleGoToStep.bind(this);

    if (props.match !== undefined) {
      var startStep = (props.match.params.step === undefined ? Consts.STEP_1 : props.match.params.step);
    }

    this.state = {
      propertyId: props.match.params.propertyId,
      property: undefined,
      token: '',
      SelectedTab: startStep,
      InputValues: {},
      InputValuesChanged: {},
      ValidationResults: undefined,
      Loading: false,
      ClosingCosts: undefined
    };
  }

  async componentDidMount () {
    try {
      this.setState({ Loading: true, InputValuesChanged: {} });
      const requestConfig = {
        headers: {
          'Content-Type': Consts.HEADER_APPLICATION_JSON,
          Authorization: (await Auth.currentSession()).getIdToken().getJwtToken()
        }
      };

      if (this.state.propertyId !== undefined) {
        const propertyListingUrl = `${Consts.API_SELLER_PROPERTY_LISTING}/${this.state.propertyId}/${await PostDataGenerator.GetUserId(Auth)}`;
        const propertyListingResponse = await axios.get(propertyListingUrl, requestConfig);
        const property = propertyListingResponse.data.Property;

        const includes = [
          Consts.DATA_SECTION_SELLER_LMH,
          Consts.DATA_SECTION_SELLER_LMA,
          Consts.DATA_SECTION_SELLER_LMF
        ];

        const url = `${Consts.API_SELLER_PRIMARY_QUESTIONNAIRE}/${this.state.propertyId}/${await PostDataGenerator.GetUserId(Auth)}/?include=${includes.join(',')}`;
        const response = await axios.get(url, requestConfig);

        let inputValues = this.state.InputValues;
        if (inputValues === undefined) {
          inputValues = {};
        }

        var fieldDefs = FieldDef.ListingFields;
        includes.forEach(dataSection => {
          const result = this.ConvertData(dataSection, fieldDefs, response.data);
          inputValues = { ...inputValues, ...result };
        });

        var propertyCity;
        var propertyType;
        var listPrice;
        var totalLoan;
        try {
          propertyCity = property.PropertyData.SubjectProperty.SitusAddress.City;
          propertyType = property.PropertyData.SiteInformation.LandUse;
          listPrice = inputValues.Listing_LMF_432_List_Price;
          totalLoan = inputValues.Listing_LMF_088_Total_Loans;
        } catch (error) {
          console.log(error);
        }
        await this.GetClosingCosts(listPrice, totalLoan, propertyCity, propertyType);

        this.setState({ InputValues: inputValues, property, Loading: false });
      } else {
        this.setState({ InputValues: undefined, property: undefined, Loading: false });
      }
    } catch (error) {
      console.log(error);
      this.setState({ Loading: false });
      toastr.error('Load listing info', 'Please refresh page and try again');
    }
  }

  async GetClosingCosts (listPrice, totalLoan, propertyCity, propertyType) {
    if (listPrice !== undefined && totalLoan !== undefined && propertyCity !== undefined && propertyType !== undefined) {
      const requestConfig = {
        headers: {
          'Content-Type': Consts.HEADER_APPLICATION_JSON,
          Authorization: (await Auth.currentSession()).getIdToken().getJwtToken()
        }
      };

      const closingCostsBody = {
        ListPrice: parseInt(listPrice),
        TotalLoan: parseInt(totalLoan),
        PropertyType: propertyType.toLowerCase(),
        City: propertyCity
      };
      const closingCostsResponse = await axios.post(Consts.API_SELLER_CLOSING_COSTS, closingCostsBody, requestConfig);
      const ClosingCosts = closingCostsResponse.data.ClosingCosts;
      this.setState({ ClosingCosts });
    } else {
      this.setState({ ClosingCosts: undefined });
    }
  }

  async SaveNextHandler (nextStep, dataSections) {
    const result = await this.SaveData(false, dataSections);
    if (result) {
      this.handleGoToStep(nextStep);
    }
  }

  async SaveHandler (dataSections) {
    await this.SaveData(true, dataSections);
  }

  async SaveData (displaySucceededMessage, dataSections) {
    if (dataSections === undefined || dataSections.length === 0) {
      if (displaySucceededMessage) {
        toastr.success('Save listing info', 'Succeeded');
      }
      return true;
    }

    const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
    this.setState({ ValidationResults: validationResults });
    if (validationResults !== undefined && validationResults.length > 0) {
      return false;
    }

    this.setState({ Loading: true });
    const selectedTab = this.state.SelectedTab;

    try {
      await this.SaveStepData(selectedTab, dataSections);
      this.UpdateInputValuesChanged(selectedTab, false);

      // if (section === Consts.STEP_3) {
      //   await this.SaveStep3Data();
      //   this.UpdateInputValuesChanged(Consts.STEP_3, false);
      // }

      // if (section === Consts.STEP_3_5) {
      //   await this.SaveStep35Data();
      //   this.UpdateInputValuesChanged(Consts.STEP_3_5, false);
      // }

      // if (section === Consts.STEP_6) {
      //   await this.SaveStep6Data();
      //   this.UpdateInputValuesChanged(Consts.STEP_6, false);
      // }

      // if (section === Consts.STEP_7) {
      //   await this.SaveStep7Data();
      //   this.UpdateInputValuesChanged(Consts.STEP_7, false);
      // }

      // if (section === Consts.STEP_8) {
      //   await this.SaveStep8Data();
      //   this.UpdateInputValuesChanged(Consts.STEP_8, false);
      // }

      if (displaySucceededMessage === true) {
        toastr.success('Save listing info', 'Succeeded');
      }
      this.setState({ Loading: false });
      return true;
    } catch (Error) {
      console.log(Error);
      toastr.error('Save listing info', 'Please refresh page and try again');
      this.setState({ Loading: false });
      return false;
    }
  }

  async SaveStepData (selectedTab, dataSections) {
    // skip if data not changed
    if (this.state.InputValuesChanged[selectedTab] === undefined || this.state.InputValuesChanged[selectedTab] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.ListingFields;
    const postData = {};
    dataSections.forEach(section => {
      postData[section] = PostDataGenerator.GetPostData(this.state.InputValues, fieldDefs, section);
    });

    const body = {
      PropertyId: this.state.propertyId,
      UserId: await PostDataGenerator.GetUserId(Auth),
      Data: postData
    };
    const requestConfig = await PostDataGenerator.GetRequestConfig(Auth);
    await axios.post(Consts.API_SELLER_PRIMARY_QUESTIONNAIRE, body, requestConfig);
  }

  ConvertData (dataSection, fieldDefs, responseData) {
    const sectionData = responseData.PrimaryQuestionnaire[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(sectionData, fieldDefs, dataSection);
    return sectionDataResult;
  }

  async ValueChangeHandler (event, fieldName, fieldType, value) {
    const inputValues = this.state.InputValues;

    var targetValue;
    if (fieldName !== undefined && fieldType !== undefined) {
      targetValue = value;
      inputValues[fieldName] = value;
    } else {
      if (event.target.type === 'checkbox') {
        targetValue = event.target.checked;
        inputValues[event.target.name] = targetValue;
      } else {
        targetValue = event.target.value;
        inputValues[event.target.name] = targetValue;
      }
    }

    var propertyCity;
    var propertyType;
    var listPrice;
    var totalLoan;
    try {
      const property = this.state.property;
      propertyCity = property.PropertyData.SubjectProperty.SitusAddress.City;
      propertyType = property.PropertyData.SiteInformation.LandUse;
      listPrice = inputValues.Listing_LMF_432_List_Price;
      totalLoan = inputValues.Listing_LMF_088_Total_Loans;
    } catch (error) {
      console.log(error);
    }
    await this.GetClosingCosts(listPrice, totalLoan, propertyCity, propertyType);

    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputValuesChanged (step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  handleStepNavClick (event, dataSections) {
    if (this.state.SelectedTab === Consts.STEP_2 || this.state.SelectedTab === Consts.STEP_3) {
      const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
      this.setState({ ValidationResults: validationResults });
      if (validationResults === undefined || validationResults.length === 0) {
        this.setState({ SelectedTab: event.target.id });
      }
    } else {
      this.setState({ SelectedTab: event.target.id });
    }
  }

  ValidateInputValue (dataSections, inputValues) {
    const fieldDefs = FieldDef.ListingFields;
    var validateFields = [];
    if (dataSections !== undefined) {
      dataSections.forEach(dataSection => {
        const dataFields = fieldDefs.filter(field => field.DataSection === dataSection);
        dataFields.forEach(dataField => {
          validateFields.push(dataField);
        });
      });
    }

    return Validator.Validate(inputValues, validateFields);
  }

  handleGoToStep (step) {
    this.setState({ SelectedTab: step });
    window.scrollTo(0, 0);
  }

  render () {
    const stepProps = {
      CurrentTab: this.state.SelectedTab,
      SaveHandler: this.SaveHandler,
      SaveNextHandler: this.SaveNextHandler,
      ValueChangeHandler: this.ValueChangeHandler,
      GoToStepHandler: this.handleGoToStep,
      ValidationResults: this.state.ValidationResults,
      InputValues: this.state.InputValues,
      Auth,
      Property: this.state.property,
      ClosingCosts: this.state.ClosingCosts
    };

    return (
      <div>
        <BlockUi
          tag='div'
          blocking={this.state.Loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <div className='container'>
            <form action='#' method='POST' data-name='buyer-form' id='buyer-form' encType='multipart/form-data'>
              <Navigator
                CurrentTab={this.state.SelectedTab}
                SaveNextHandler={this.SaveNextHandler}
                onStepNavClick={this.handleStepNavClick}
              />
              <ProfileStep1 {...stepProps} />
              <ProfileStep2 {...stepProps} />
              <ProfileStep3 {...stepProps} />
              <ProfileStep4 {...stepProps} />
              <ProfileStep4Confirm {...stepProps} />
            </form>
          </div>
        </BlockUi>
      </div>
    );
  }
}

export default withCustomAuthenticator(Listing);
