import React from 'react';
import PhoneInput from 'react-phone-number-input';
import * as Utils from './Utils';
import 'react-phone-number-input/style.css';
import './IntlTelInputer.css';

export default function IntlTelInputer (props) {
  const {
    FieldName,
    Required,
    FieldDef,
    ValueChangeHandler,
    ValidationResults,
    InputValues,
    TitleUnderInput
  } = props;

  let value = '';
  if (InputValues !== undefined && InputValues[FieldName] !== undefined) {
    value = InputValues[FieldName];
  }

  return (
    <>
      {(FieldDef.Title !== '' && (TitleUnderInput === undefined || TitleUnderInput === false)) &&
        <div className='t_texts'>{FieldDef.Title}</div>}

      <PhoneInput
        className={Utils.GetClassNameWithoutBaseClass(ValidationResults, FieldName, Required)}
        placeholder='Enter phone number'
        defaultCountry='US'
        value={value}
        onChange={ValueChangeHandler.bind(this, new CustomEvent('tel_onchange'), FieldName, FieldDef.Type)}
        disabled={props.ReadOnly === true}
      />

      {Utils.GetValidationResult(ValidationResults, FieldName)}
      {(FieldDef.Title !== '' && TitleUnderInput === true) &&
        <div>{FieldDef.Title}</div>}

    </>
  );
}
