import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import FormYesNoExplanation from '../../Shared/FormYesNoExplanation';

export default class Tax extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>
        <FormSectionTitle Title='TAX' />
        <div className='form_inputs'>
          <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTX_x01-n' })}</div>
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTX_x01_Confirm_Paid_Tax', ExpFieldName: 'Finance_FTX_x01_T_Explanation' }} />
          <FormYesNoExplanation FieldProps={{ ...FieldProps, FieldName: 'Finance_FTX_439_Is_Foreign_Corp' }} />
          <FormYesNoExplanation FieldProps={{
            ...FieldProps,
            FieldName: 'Finance_FTX_996_Is_Non_Resident_Alien',
            SubTitle: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTX_996-n' })
          }}
          />

          <FormYesNoExplanation FieldProps={{
            ...FieldProps,
            FieldName: 'Finance_FTX_731_Is_Sold_Under_301_Exchange_Program',
            SubTitle: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTX_731-n' })
          }}
          />

          <FormYesNoExplanation FieldProps={{
            ...FieldProps,
            FieldName: 'Finance_FTX_804_Is_Investement_Property',
            SubTitle: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTX_804-n' })
          }}
          />
        </div>
      </>
    );
  }
}
