import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class VehicleParking extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Vehicle & Parking' />
        <div className='form_inputs'>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_300_Has_Parking' })}</div>
          <br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFV_300_Has_Parking', Consts.OPTION_YES_VALUE) &&
              <>
                <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_056_Spaces' })}</div>
                <br />
              </>
          }

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_276_Parking_Features' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFV_276_Parking_Features', 'Garage') &&
              <>
                <br />
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_743_x1_Garage_Type' })}</div>
                <br />
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_434_Is_Automatcic_Garage' })}</div>
                <br />
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_434_t_Garage_Remotes' })}</div>
                <br />
              </>
          }
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFV_276_Parking_Features', 'Driveways') &&
              <>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_268_Defects' })}</div>
                {
                  InputValuesHelper.HasInputValue(InputValues, 'Features_SFV_268_Defects', Consts.OPTION_YES_VALUE) &&
                    <>
                      <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_268_t_Text' })}</div>
                      <br />
                    </>
                }
              </>
          }

          <div className='inputer_form twothirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_790_Defects' })}</div>
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFV_790_Defects', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFV_790_t_Text_Explanation' })}</div>
          }
        </div>
      </>
    );
  }
}
