import React from 'react';

export default function FormSectionTitle (props) {
  return (
    <>
      <div className='pro_tab alternatess'>
        <div className='tab1'>{props.Title}</div>
        {props.SubTitle !== undefined &&
          <>
            <div className='tab_divider' />
            <div className='tab2'>{props.SubTitle}</div>
          </>}
      </div>
    </>
  );
}
