import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import './modal.css';
import * as Consts from '../../shared/Consts';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

export function ContactUsForm (props) {
  const [message, setMessage] = useState('');
  const [messageInvalid, setMessageInvalid] = useState(false);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);

  return (
    <>
      <span className='s_block' onClick={() => { setShowModal(true); }}>{props.children}</span>
      <Modal
        size='lg'
        show={showModal}
        onHide={() => { setShowModal(false); }}
        onExited={() => { setMessageInvalid(false); setMessage(''); setLoading(false); }}
        centered
      >
        <BlockUi
          style={{ zIndex: '1500' }}
          blocking={loading}
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <Modal.Header>
            <Modal.Title className='title2'>Contact us</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className='inputer2' style={{ marginTop: '0px' }}>
              <div className='t_texts' style={{ marginBottom: '0px' }}>Message</div>
              <div className=''>
                <textarea
                  className={messageInvalid === true ? 'form2 required validation-failed' : 'form2 required'}
                  onChange={(event) => { setMessage(event.target.value); }}
                  rows={5}
                  cols={20}
                  maxLength={400}
                  style={{ height: '120px', textIndent: '0px' }}
                />
                {messageInvalid === true &&
                  <span className='__validation_error_text __validation_error'>
                    {Consts.VALIDATION_ERROR_REQUIRED_FIELD}
                  </span>}
              </div>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant='secondary'
              className='save_btn animated_fastest'
              onClick={() => { setShowModal(false); }}
            >
            Close
            </Button>
            <Button
              variant='primary'
              className='next_btn animated_fastest'
              onClick={async () => {
                if (message.trim() === '') {
                  setMessageInvalid(true);
                } else {
                  setLoading(true);
                  setMessageInvalid(false);
                  const result = await props.handleSubmit(message);
                  if (result === true) {
                    setShowModal(false);
                  } else {
                    setLoading(false);
                  }
                }
              }}
            >
            Submit
            </Button>
          </Modal.Footer>
        </BlockUi>
      </Modal>
    </>
  );
}
