import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/FinanceTaxTitle';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class TitleHolderLLC extends React.Component {
  render () {
    // const {
    //   InputValues
    // } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FinanceTaxTitleFields
    };

    return (
      <>

        <FormSectionTitle Title='STATEMENT OF INFORMATION' SubTitle='LLC Title Holder' />
        <div className='form_inputs'>
          <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTC_171_T_Llc_Tax_Id' })} <span className='greyer'>If you do not have a tax ID number, please use your social Secuity Number</span></div>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTC_171_T_Llc_Tax_Id', TitleUnderInput: true })}</div>
          <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Finance_FTC_171-n' })}</div>
          <div className='t_texts'>Upload LLC Documents</div>
          {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Finance_FTC_171-u_Upload_Llc_Documents' })}
        </div>
      </>
    );
  }
}
