import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/SystemsFeatures';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class SafetySecurity extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <FormSectionTitle Title='Safety & Security' />
        <div className='form_inputs'>
          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFS_604_Additional_Features' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFS_604_Additional_Features', 'WindowSecuirtyBars') &&
              <>
                <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFS_756_Quick_Release_Mechanism' })}</div><br />
              </>
          }

          <div className='inputer_form'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFS_847_Defects' })}</div><br />
          {
            InputValuesHelper.HasInputValue(InputValues, 'Features_SFS_847_Defects', Consts.OPTION_YES_VALUE) &&
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Features_SFS_847_t_Text_Explanation' })}</div>
          }

        </div>
      </>
    );
  }
}
