import * as Consts from './Consts';

export const Convert = (responseData, fieldDefinitions, dataSection) => {
  const result = {};

  if (responseData !== undefined) {
    const fields = fieldDefinitions.filter(item => item.DataSection === dataSection);
    fields.forEach(field => {
      if (responseData[field.DataField] !== undefined && responseData[field.DataField] !== null) {
        if (field.DataType === Consts.DATA_TYPE_BOOLEAN && field.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
          if (responseData[field.DataField] === true) {
            result[field.FieldName] = Consts.OPTION_YES_VALUE;
          } else if (responseData[field.DataField] === false) {
            result[field.FieldName] = Consts.OPTION_NO_VALUE;
          } else {
            result[field.FieldName] = undefined;
          }
        } else {
          result[field.FieldName] = responseData[field.DataField];
        }
      } else {
        result[field.FieldName] = undefined;
        // if (field.DataType === Consts.DATA_TYPE_BOOLEAN) {
        //   result[field.FieldName] = Consts.DATA_TYPE_BOOLEAN_DEFAULT_VALUE;
        // } else if (field.DataType === Consts.DATA_TYPE_STRING) {
        //   result[field.FieldName] = Consts.DATA_TYPE_STRING_DEFAULT_VALUE;
        // }
      }
    });
  }

  return result;
};
