import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import * as PostDataGenerator from '../../../shared/PostDataGenerator';
import { withRouter } from 'react-router-dom';
import imageArrowW from '../../../images/arrow_w.svg';
import axios from 'axios';
import uuid from 'uuid';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

class ProfileStep2
  extends React.Component {
  constructor () {
    super();

    this.handleVerifyPropertyAddress = this.handleVerifyPropertyAddress.bind(this);
    this.handlePropertyAddressChanged = this.handlePropertyAddressChanged.bind(this);

    this.state = {
      PropertyAddress: '',
      InvalidPropertyAddress: undefined,
      PropertyErrorMessage: undefined,
      VerifingPropertyAddress: false
    };
  }

  handlePropertyAddressChanged (event) {
    this.setState({ PropertyAddress: event.target.value });
  }

  componentDidMount () {
    // console.log(this.props.match.params.propertyId);
  }

  async handleVerifyPropertyAddress () {
    if (this.state.PropertyAddress !== undefined && this.state.PropertyAddress.trim() !== '') {
      this.setState({ VerifingPropertyAddress: true });
      const propertyId = uuid();
      // const dataTreeBaseUrl = 'https://dtapiuat.datatree.com:443';
      // var token = '';

      try {
      //   const authenticateUrl = `${dataTreeBaseUrl}/api/Login/Authenticate`;
      //   const authenticateBody = { Username: 'DTAPI_RiverChase_UAT', Password: 'RiverChaseDTAPI@1UAT' };
      //   const authenticateRequestConfig = {
      //     headers: {
      //       'Content-Type': 'application/json',
      //       IncludeHash: true
      //     }
      //   };
      //   const authResponse = await axios.post(authenticateUrl, authenticateBody, authenticateRequestConfig);
      //   token = authResponse.data;
      // } catch (error) {
      //   console.log(error);
      //   toastr.error('Verify property address', 'Please refresh page and try again');
      //   this.setState({ VerifingPropertyAddress: false });
      //   return;
      // }

        // try {
        //   const reportUrl = `${dataTreeBaseUrl}/api/Report/GetReport`;
        //   const reportBody = {
        //     ProductNames: [
        //       'PropertyDetailReport', 'DataTreeAVM'
        //     ],
        //     SearchType: 'FullAddress',
        //     FullAddress: this.state.PropertyAddress,
        //     ReferenceId: propertyId
        //   };
        //   const reportRequestConfig = {
        //     headers: {
        //       'Content-Type': 'application/json',
        //       Authorization: token
        //     }
        //   };

        //   const reportResponse = await axios.post(reportUrl, reportBody, reportRequestConfig);
        //   console.log(reportResponse.status);
        //   if (reportResponse.status === 200) {
        //     const responseData = reportResponse.data;
        //     if (responseData.MaxResultsCount !== undefined && responseData.MaxResultsCount === 1) {
        //       var propertyData = {};
        //       const propertyDetail = responseData.Reports.find(report => report.ReportName === 'PropertyDetailReport');
        //       if (propertyDetail !== undefined) {
        //         propertyData = propertyDetail.Data;
        //       }
        //       const propertyAVM = responseData.Reports.find(report => report.ReportName === 'DataTreeAVM');
        //       if (propertyAVM !== undefined) {
        //         propertyData = { ...propertyData, ValuationSummary: propertyAVM.Data.ValuationSummary };
        //       }

        //       console.log(propertyData);

        const propertyBody = {
          PropertyId: propertyId,
          PropertyAddress: this.state.PropertyAddress,
          UserId: await PostDataGenerator.GetUserId(this.props.Auth),
          // PropertyData: JSON.stringify(propertyData)
          PropertyData: JSON.stringify({ description: 'tbd' })
        };
        const propertyRequestConfig = await PostDataGenerator.GetRequestConfig(this.props.Auth);
        await axios.post(Consts.API_SELLER_PROPERTY_LISTING, propertyBody, propertyRequestConfig);

        this.props.history.push(`${this.props.match.path}/${propertyId}/${this.props.CurrentTab}`);
        this.setState({ InvalidPropertyAddress: false, PropertyErrorMessage: undefined, VerifingPropertyAddress: false });
        //   } else if (responseData.MaxResultsCount !== undefined && responseData.MaxResultsCount > 1) {
        //     this.setState({ InvalidPropertyAddress: true, PropertyErrorMessage: 'Multiple Matching Properties Found.' });
        //   } else {
        //     this.setState({ InvalidPropertyAddress: true, PropertyErrorMessage: 'Incorrect property address.' });
        //   }
        // } else {
        //   this.setState({ InvalidPropertyAddress: true, PropertyErrorMessage: 'Incorrect property address.' });
        // }
      } catch (error) {
        console.log(error);
        toastr.warning('Verify property address', 'Please check the address and submit again!');
        this.setState({ VerifingPropertyAddress: false });
      }
    } else {
      this.setState({ InvalidPropertyAddress: true, PropertyErrorMessage: Consts.VALIDATION_ERROR_REQUIRED_FIELD });
    }
  }

  render () {
    const {
      InputValues,
      CurrentTab
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    if (CurrentTab !== Consts.STEP_2) {
      return null;
    }

    var propertyAddressClass = 'form2 required';
    if (this.state.InvalidPropertyAddress === true) {
      propertyAddressClass += ' validation-failed';
    }

    return (
      <>
        {this.props.match.params.propertyId === undefined &&
          <BlockUi
            tag='div'
            blocking={this.state.VerifingPropertyAddress}
            loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
          >
            <div id='step2' className='main_form_info steppers'>
              <div className='inputer_form halfs'>
                <div className='t_texts'>Property Address</div>
                <input
                  name='PropertyAddress'
                  type='text'
                  className={propertyAddressClass}
                  onChange={this.handlePropertyAddressChanged}
                  value={this.state.PropertyAddress}
                />
                {
                  (this.state.InvalidPropertyAddress === true) &&
                    <span className='__validation_error_text __validation_error'>
                      {this.state.PropertyErrorMessage}
                    </span>
                }
              </div><br />
              <div className='prev'>
                <div onClick={this.handleVerifyPropertyAddress} className='submit_btn animated_fastest'>Submit <div className='lm_arrow'><img src={imageArrowW} width='100%' alt='Arrow W' /></div></div>
              </div>
            </div>
          </BlockUi>}

        {(this.props.match.params.propertyId !== undefined && this.props.Property !== undefined) &&
          <>
            <div id='step2' className='main_form_info steppers'>
              <div className='inputer_form halfs'>
                <div className='title2'>Property Address</div>
                <div className='t_texts'>{this.props.Property.PropertyAddress}</div>
              </div>

              <div className='title2'><div className='number_title'>02</div> Profile</div>
              <div className='b_texts'>We need to collect some information to prepare offer documents and to share with our transaction partners.</div>

              <div className='pro_tab'>
                <div className='tab1'>Authorization</div>
              </div>
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMA_787_Trust' })}</div>
              {
                InputValuesHelper.GetInputValue(InputValues, 'Listing_LMA_787_Trust') === Consts.OPTION_YES_VALUE &&
                  <>
                    <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMA_787_Contact_Person' })}</div><br />
                  </>
              }
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMA_426_Entity' })}</div>
              {
                InputValuesHelper.GetInputValue(InputValues, 'Listing_LMA_426_Entity') === Consts.OPTION_YES_VALUE &&
                  <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMA_426_Contact_Person' })}</div>
              }

              <div className='pro_tab'>
                <div className='tab1'>Tell us who you are</div>
              </div>

              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_635_760_First_Name' })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_635_152_Last_Name' })}</div>
              <div className='inputer_form greyer'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Listing_LMH_698' })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_698_354_Primary_Phone' })}</div><br />
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_698_362_Email_Address' })}</div><br />
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_875_Preferredmethod' })}</div><br />
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_385_MailingAddressSameAsPropertyAddress' })}</div><br />

              {InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_385_MailingAddressSameAsPropertyAddress') === Consts.OPTION_NO_VALUE &&
                <>
                  <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_612_Street_Address' })}</div>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_992_City' })}</div>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_992_State' })}</div>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_677_Postal_Code' })}</div><br />
                </>}
              <div className='t_texts' style={{ marginBottom: '5px' }}>
                <span>Would you like to add an Information Recipient?</span>
              </div>
              <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px', maxWidth: '600px' }}>
                <span>Is there someone else you would like to include on communications (for example, a spouse or significant other, attorney, CPA or business advisor) as an Information Recipient? This person will only receive information, they will not be listed as a purchaser.</span>
              </div>
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Inforecipient', Title: undefined })}</div><br />

              {InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_839_Inforecipient') === Consts.OPTION_YES_VALUE &&
                <>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_First_Name' })}</div>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Last_Name' })}</div><br />
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Email' })}</div>
                  <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Phone' })}</div>
                </>}
            </div>

            <FooterMiddleStep
              SaveHandler={() => { this.props.SaveHandler(DataSections); }}
              PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.STEP_1); }}
              SaveNextHandler={() => { this.props.SaveNextHandler(Consts.STEP_3, DataSections); }}
            />
          </>}
      </>
    );
  }
}

export default withRouter(ProfileStep2);

const DataSections = [
  Consts.DATA_SECTION_SELLER_LMH,
  Consts.DATA_SECTION_SELLER_LMA
];
